import React from "react";
import MenuSection from "./MenuSection";
import MenuItemSeparator from "./MenuItemSeparator";
import MenuItem from "./MenuItem";

export default class MenuList extends React.Component {
	render() {
		const { currentUrl, menuConfig, layoutConfig } = this.props;
		const InitialPage = window.localStorage.getItem("pageName") || ""
		// console.log("init page", InitialPage)
		
		var InitAside;
		if (InitialPage === "perencanaan"){
			InitAside = menuConfig.asidePerencanaan
		}else if(InitialPage === "project"){
			InitAside = menuConfig.asideProject
		} else if(InitialPage === "keuangan"){
			InitAside = menuConfig.asideKeuangan
		}else if(InitialPage === "gudang"){
			InitAside = menuConfig.asideGudang
		}
		else if(InitialPage === "logistik"){
			InitAside = menuConfig.asideLogistik
		}
		else if(InitialPage === "pembelian"){
			InitAside = menuConfig.asidePembelian
		}else if(InitialPage === "siteEng"){
			InitAside = menuConfig.asideSiteEng
		}else if(InitialPage === "progress"){
			InitAside = menuConfig.asideProgress
		}else{
			InitAside = menuConfig.aside
		}
		return InitAside.items.map((child, index) => {
			return (
				<React.Fragment key={`menuList${index}`}>
					{child.section && <MenuSection item={child} />}
					{child.separator && <MenuItemSeparator item={child} />}
					{child.title && (
						<MenuItem
							item={child}
							currentUrl={currentUrl}
							layoutConfig={layoutConfig}
						/>
					)}
				</React.Fragment>
			);
		});
	}
}
