import React, {Fragment} from "react"
import {Form} from "react-bootstrap";
import {
	Portlet,
	PortletBody,
	PortletHeader,
	PortletHeaderToolbar
} from "../../../partials/content/Portlet";
import axios from '../../../../config/axios'
import {toast} from "react-toastify";
import format from "format-number";
import {connect} from "react-redux";

class ProjectOverview extends React.Component{
	constructor(props){
		super(props)
		this.state = {
			data        : [],
			isLoading   : true
		}
	}
	
	loadData() {
		const config = {
			headers: {
				'Accept' 		: 'application/json',
				'Content-Type' 	: 'aplication/x-www-form-urlencoded;charset=UTF-8;multipart/form-data',
				'Authorization' : 'Bearer '+ this.props.authToken
			},
		};
		
		let formData = new FormData()
		formData.append('id_project', localStorage.getItem("id_project"))
		axios.post(`project/get-project`,formData, config)
			.then(res => {
				const result = res.data.data
				if(res.status){
					this.setState({
						data        : result,
						isLoading   : false
					})
				}else{
					toast['error']('Terjadi Kesalahan Di Server')
				}
			})
			.catch(error => {
				toast['error']('Terjadi Kesalahan Di Server')
			})
	}
	
	componentDidMount() {
		this.loadData()
	}
	
	render(){
		let {data} = this.state
		return(
			<div className="row">
				<div className="col-xl-6">
					<Portlet fluidHeight={true}>
						<PortletHeader
							title="PROJECT OVERVIEW"
						/>
						
						<PortletBody>
							<div className="kt-widget12">
								<div className="kt-widget12__content">
									<div className="row">
										<div className="col-md-12">
											<Form.Group controlId="exampleForm.ControlInput1">
												<Form.Label>Nama Proyek</Form.Label>
												<Form.Control name="nama-proyek" as="textarea" rows="3" value={data.project_name} disabled />
											</Form.Group>
										</div>
										<div className="col-md-6">
											<Form.Group controlId="exampleForm.ControlInput1">
												<Form.Label>Lokasi</Form.Label>
												<Form.Control name="lokasi-proyek" type="text" placeholder="" defaultValue={data.location} disabled/>
											</Form.Group>
											<Form.Group controlId="exampleForm.ControlInput1">
												<Form.Label>Tahun Proyek</Form.Label>
												<Form.Control name="tahun-proyek" type="number" placeholder="" defaultValue={data.year} disabled/>
											</Form.Group>
											<Form.Group controlId="exampleForm.ControlInput1">
												<Form.Label>Pemilik Proyek</Form.Label>
												<Form.Control name="pemilik-proyek" type="text" placeholder="" defaultValue={data.owner} disabled/>
											</Form.Group>
										</div>
										<div className="col-md-6">
											<Form.Group controlId="exampleForm.ControlInput1">
												<Form.Label>Nilai Kontrak</Form.Label>
												<Form.Control name="nilai_kontrak_proyek" type="text" placeholder="" defaultValue={format({prefix: 'Rp ', integerSeparator: "."})(data.contract_value)} disabled/>
											</Form.Group>
											<Form.Group controlId="exampleForm.ControlSelect1">
												<Form.Label>Jenis Kontrak</Form.Label>
												<Form.Control as="select" disabled >
													<option>Swa Kelola</option>
													<option>Kontraktual</option>
												</Form.Control>
											</Form.Group>
											<div className="row">
												<div className="col-md-6">
													<Form.Group controlId="exampleForm.ControlInput1">
														<Form.Label>Tanggal Mulai</Form.Label>
														<Form.Control name="tgl-mulai-proyek" type="text" placeholder="" defaultValue={data.start_date} disabled/>
													</Form.Group>
												</div>
												<div className="col-md-6">
													<Form.Group controlId="exampleForm.ControlInput1">
														<Form.Label>Tanggal Selesai</Form.Label>
														<Form.Control name="tgl-selesai-proyek" type="text" placeholder="" defaultValue={data.end_date} disabled/>
													</Form.Group>
												</div>
											</div>
										</div>
									</div>
								</div>
							</div>
						</PortletBody>
					</Portlet>
				</div>
			</div>
		)
	}
}

const mapStateToProps = ({ auth: { user, authToken } }) => ({
	user, authToken
});

export default connect(mapStateToProps)(ProjectOverview);