import React, {Fragment} from "react"
import Grid from "@material-ui/core/Grid";
import {Container, InputAdornment, makeStyles} from "@material-ui/core";
import {Form, Modal, OverlayTrigger, Spinner, Table, Tooltip} from "react-bootstrap";
import TextField from "@material-ui/core/TextField";
import {Button, Badge} from  "react-bootstrap"
import {Link} from "react-router-dom";
import {toast} from "react-toastify";
import axios from '../../../../config/axios'
import DeleteForeverOutlinedIcon from '@material-ui/icons/DeleteForeverOutlined';
import RemoveRedEyeOutlinedIcon from '@material-ui/icons/RemoveRedEye';
import EditOutlinedIcon from '@material-ui/icons/Edit';
import moment from 'moment';
import {
	PortletHeader,
	PortletHeaderToolbar
} from "../../../partials/content/Portlet";
import {connect} from "react-redux";

const useStyles = makeStyles(theme => ({
	root: {
		flexGrow: 1,
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',
		color: theme.palette.text.secondary,
	},
}));

class ListRequestMaterial extends React.Component{
	constructor(props){
		super(props)
		this.state = {
			//start pagination
			paging          : 25,
			perPage         : 0,
			pageCount       : 0,
			currentPage     : 0,
			//end pagination
			
			//start filter
			keyword         : "",
			//end filter
			
			no_req      : "",
			show        : false,
			modalShow   : false,
			children    : [],
			data        : [],
			isLoading   : true,
		}
		this.handleReset    = this.handleReset.bind(this)
		this.handleShow = this.handleShow.bind(this)
		this.handleClose = this.handleClose.bind(this);
	}
	
	handleReset(){
		this.setState({
			isLoading       : true,
			paging          : 25,
			keyword         : '',
		})
	}
	
	handleClose() {
		this.setState({
			no_req  : "",
			show: false,
			modalShow: false,
			children:null
		});
	}
	
	handleShow(e, item) {
		// console.log('asf', item)
		let child = []
		item.children.map((ch, idx)=>{
			child.push(ch)
		})
		this.setState({
			no_req  : item.no_request,
			modalShow: true,
			show: true,
			children:child
		});
	}
	
	loadData() {
		const config = {
			headers: {
				'Accept' 		: 'application/json',
				'Content-Type' 	: 'aplication/x-www-form-urlencoded;charset=UTF-8;multipart/form-data',
				'Authorization' : 'Bearer '+ this.props.authToken
			},
		};
		
		let formData = new FormData()
		
		formData.append('page'            , this.state.currentPage + 1)
		formData.append('paging'          , this.state.paging)
		formData.append('keyword'         , this.state.keyword)
		
		formData.append('project_id', localStorage.getItem("id_project"))
		formData.append('section_id', 4)
		formData.append('user_id', this.props.user.id)
		axios.post(`requests/get`,formData, config)
			.then(res => {
				var result;
				if(res.data.data !== undefined){
					result = res.data.data
				}else{
					result = []
				}
				// console.log('res', res)
				if(res.data.status){
					this.setState({
						data        : result.data,
						isLoading   : false
					})
				}else{
					// toast['error']('Terjadi Kesalahan Di Server')
					this.setState({
						isLoading   : false,
					})
				}
			})
			.catch(error => {
				toast['error']('Terjadi Kesalahan di Server')
				this.setState({
					isLoading   : false,
				})
			})
	}
	
	componentDidMount() {
		this.loadData()
	}
	
	render() {
		let no = 0
		let {children} = this.state
		let {data} = this.state
		let {isLoading} = this.state
		var tabelDetail;
		if (this.state.modalShow === true) {
			tabelDetail = (
				<Modal
					show={this.state.modalShow}
					onHide={this.handleClose}
					size="lg"
					aria-labelledby="contained-modal-title-vcenter"
					centered
				>
					<Modal.Header closeButton>
						<Modal.Title id="contained-modal-title-vcenter">
							{this.state.no_req}
						</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<Table responsive bordered hover>
							<thead>
							<tr>
								<th><strong>No</strong></th>
								<th><strong>Item</strong></th>
								<th><strong>Satuan</strong></th>
								<th><strong>Jumlah</strong></th>
							</tr>
							</thead>
							<tbody>
							{
								children.length !== 0 && children !== null ?
									children.map((itemx, idx) => {
										return (
											<tr key={idx}>
												<td>1</td>
												<td>{itemx.item_name}</td>
												<td>{itemx.uom}</td>
												<td>{itemx.quantity}</td>
											</tr>
										)
									})
									:
									""
							}
							</tbody>
						</Table>
					</Modal.Body>
					<Modal.Footer>
						<Button onClick={this.handleClose}>Close</Button>
					</Modal.Footer>
				</Modal>
			)
		}else{
			tabelDetail = ""
		}
		return(
			<Fragment>
				<Container fixed>
					{tabelDetail}
					<div className={useStyles.root}>
						<div className="kt-portlet kt-portlet--height-fluid">
							<PortletHeader
								title="List Request Material"
								toolbar={
									<PortletHeaderToolbar>
										<Link to="/site-eng/create-mat">
											<Button variant="success">Tambah</Button>
										</Link>
									</PortletHeaderToolbar>
								}
							/>
							<div className="kt-portlet__body">
								<div className="kt-widget5">
									<div className="kt-widget5__item ">
										<Grid container spacing={3}>
										    <Grid item md={12} xs={12}>
											    <div className="kt-section">
												    <div className="row">

													    <div className="col-md-12">
														    <Form.Row>
															    <Form.Group controlId="show" style={{marginLeft: 5, marginRight: 5}}>
																    <Form.Label>Tampilkan</Form.Label>
																    <Form.Control as="select">
																	    <option>10</option>
																	    <option>25</option>
																	    <option>50</option>
																	    <option>100</option>
																    </Form.Control>
															    </Form.Group>
															
															    <Form.Group sm={12} controlId="pencarian" style={{marginLeft: 5, marginRight: 5}}>
																    <Form.Label>Pencarian</Form.Label>
																    <Form.Control placeholder="Cari......" />
															    </Form.Group>
															
															    <Button variant="primary" style={{margin:"auto 5px auto 5px"}}>
																    Submit
															    </Button>
															
															    <Button variant="dark"
															            onClick={this.handleReset}
															            style={{margin:"auto 5px auto 5px"}}>
																    Reset
															    </Button>
														
														    </Form.Row>
													    </div>
													    
													    <div className="col-md-12" style={{marginTop:5}}>
													        <Table responsive bordered hover>
													            <thead>
													            <tr>
													                <th><strong>No</strong></th>
													                <th><strong>Tanggal Request</strong></th>
													                <th><strong>Nomor Request</strong></th>
													                <th><strong>Site Manager</strong></th>
													                <th><strong>Manager</strong></th>
													                <th><strong>Note</strong></th>
													                <th><strong>Action</strong></th>
													            </tr>
													            </thead>
													            <tbody>
															    {
															        data.length !== 0 && data !== null ?
																	    (
																	        data.map((item, index) => {
																	        	no += 1
																	            return(
																			        <tr key={index}>
																				        <td>{no}</td>
																				        <td>{moment(item.request_date).format('LL')}</td>
																				        <td>{item.no_request}</td>
																				        <td>
																					        {
																						        item.status_site_manager == 0 ?
																							        <Badge variant="primary">NEW</Badge> :
																							        item.status_site_manager == 1 ?
																								        <Badge variant="success">APPROVED</Badge> :
																								        <Badge variant="danger">REJECTED</Badge>
																					        }
																					        <br/>
																					        {moment(item.date_site_manager).format('LL')}
																				        </td>
																				        <td>
																					        {
																						        item.status_manager == 0 ?
																							        <Badge variant="primary">NEW</Badge> :
																							        item.status_manager == 1 ?
																								        <Badge variant="success">APPROVED</Badge> :
																								        <Badge variant="danger">REJECTED</Badge>
																					        }
																					        <br/>
																					        {moment(item.date_manager).format('LL')}
																				        </td>
																				        <td>{item.note}</td>
																				        <td style={{textAlign:"center"}}>
																					        {
																						        item.status_site_manager == 0 && item.status_manager == 0 ?
																							        <Fragment>
																								        <OverlayTrigger
																									        key={`a-${index}`}
																									        placement={`top`}
																									        overlay={
																										        <Tooltip id={`tooltip-a-${index}`}>
																											        <strong>Detail</strong>
																										        </Tooltip>
																									        }
																								        >
																									        <Button onClick={(e) => this.handleShow(e, item)} variant="outline-primary" size="sm" style={{marginRight:5}}><RemoveRedEyeOutlinedIcon/></Button>
																								        </OverlayTrigger>
																								        <OverlayTrigger
																									        key={`b-${index}`}
																									        placement={`top`}
																									        overlay={
																										        <Tooltip id={`tooltip-b-${index}`}>
																											        <strong>Edit</strong>
																										        </Tooltip>
																									        }
																								        >
																								            <Button variant="outline-warning" size="sm" style={{marginRight:5}}><EditOutlinedIcon/></Button>
																								        </OverlayTrigger>
																								        <OverlayTrigger
																									        key={`c-${index}`}
																									        placement={`top`}
																									        overlay={
																										        <Tooltip id={`tooltip-c-${index}`}>
																											        <strong>Hapus</strong>
																										        </Tooltip>
																									        }
																								        >
																							                <Button variant="outline-danger" size="sm" style={{marginRight:5}}><DeleteForeverOutlinedIcon/></Button>
																								        </OverlayTrigger>
																							        </Fragment>
																								        :
																							        // item.status_site_manager == 1 ?
																								        <OverlayTrigger
																									        key={`a-${index}`}
																									        placement={`top`}
																									        overlay={
																										        <Tooltip id={`tooltip-a-${index}`}>
																											        <strong>Detail</strong>
																										        </Tooltip>
																									        }
																								        >
																									        <Button onClick={(e) => this.handleShow(e, item)} variant="outline-primary" size="sm" style={{marginRight:5}}><RemoveRedEyeOutlinedIcon/></Button>
																								        </OverlayTrigger>
																								        // :
																								        // <Fragment>
																									    //     <Button onClick={(e) => this.handleShow(e, item)} variant="outline-primary" size="sm" style={{marginRight:5}}><RemoveRedEyeOutlinedIcon/></Button>
																									    //     <Button variant="outline-warning" size="sm" style={{marginRight:5}}><EditOutlinedIcon/></Button>
																									    //     <Button variant="outline-danger" size="sm" style={{marginRight:5}}><DeleteForeverOutlinedIcon/></Button>
																								        // </Fragment>
																					            }
																				        </td>
																			        </tr>
																		        )
																	        })
																	    )
																	    :
																	    isLoading === true ?
																    (
																	    <tr>
																		    <td colSpan={7} style={{textAlign:"center"}}>
																		        <Spinner animation="border" variant="primary" />
																		    </td>
																	    </tr>
																    )
																    :
																    (
																	    <tr>
																		    <td colSpan={7} style={{textAlign:"center"}}>
																		        Tidak ada data ditemukan . . .
																		    </td>
																	    </tr>
																    )
															    }
													            {/*<tr>
													                <td>1</td>
													                <td>REQ-001-A</td>
													                <td><Badge variant="primary">New</Badge></td>
													                <td>-</td>
													                <td style={{textAlign:"center"}}>
													                    <Button variant="outline-primary" size="sm" style={{marginRight:5}}><RemoveRedEyeOutlinedIcon/></Button>
													                    <Button variant="outline-warning" size="sm" style={{marginRight:5}}><EditOutlinedIcon/></Button>
													                    <Button variant="outline-danger" size="sm" style={{marginRight:5}}><DeleteForeverOutlinedIcon/></Button>
												                    </td>
													            </tr>*/}
													            </tbody>
													        </Table>
													    </div>
													    
												    </div>
											    </div>
										    </Grid>
										</Grid>
									</div>
								</div>
							</div>
						</div>
					</div>
				</Container>
			</Fragment>
		)
	}
}

const mapStateToProps = ({ auth: { user, authToken } }) => ({
	user, authToken
});

export default connect(mapStateToProps)(ListRequestMaterial);