import React, { useMemo } from "react";
import { useSelector } from "react-redux";
import {
	Portlet,
	PortletBody,
	PortletHeader,
	PortletHeaderToolbar
} from "../../partials/content/Portlet";
import { metronic } from "../../../_metronic";
import QuickStatsChart from "../../widgets/QuickStatsChart";
import OrderStatisticsChart from "../../widgets/OrderStatisticsChart";
import OrdersWidget from "../../widgets/OrdersWidget";
import SalesBarChart from "../../widgets/SalesBarChart";
import DownloadFiles from "../../widgets/DownloadFiles";
import NewUsers from "../../widgets/NewUsers";
import LatestUpdates from "../../widgets/LatestUpdates";
import BestSellers from "../../widgets/BestSellers";
import RecentActivities from "../../widgets/RecentActivities";
import PortletHeaderDropdown from "../../partials/content/CustomDropdowns/PortletHeaderDropdown";
import {Button, Form} from "react-bootstrap";

export default function Dashboard() {
	const { brandColor, dangerColor, successColor, primaryColor } = useSelector(
		state => ({
			brandColor: metronic.builder.selectors.getConfig(
				state,
				"colors.state.brand"
			),
			dangerColor: metronic.builder.selectors.getConfig(
				state,
				"colors.state.danger"
			),
			successColor: metronic.builder.selectors.getConfig(
				state,
				"colors.state.success"
			),
			primaryColor: metronic.builder.selectors.getConfig(
				state,
				"colors.state.primary"
			)
		})
	);
	
	const chartOptions = useMemo(
		() => ({
			chart1: {
				data: [10, 14, 18, 11, 9, 12, 14, 17, 18, 14],
				color: brandColor,
				border: 3
			},
			
			chart2: {
				data: [11, 12, 18, 13, 11, 12, 15, 13, 19, 15],
				color: dangerColor,
				border: 3
			},
			
			chart3: {
				data: [12, 12, 18, 11, 15, 12, 13, 16, 11, 18],
				color: successColor,
				border: 3
			},
			
			chart4: {
				data: [11, 9, 13, 18, 13, 15, 14, 13, 18, 15],
				color: primaryColor,
				border: 3
			}
		}),
		[brandColor, dangerColor, primaryColor, successColor]
	);
	
	return (
		<>
			<div className="row">
				<div className="col-xl-6">
					<Portlet fluidHeight={true}>
						<PortletHeader
							title="PROJECT OVERVIEW"
						/>
						
						<PortletBody>
							<div className="kt-widget12">
								<div className="kt-widget12__content">
									<div className="row">
										<div className="col-md-12">
											<Form.Group controlId="exampleForm.ControlInput1">
												<Form.Label>Nama Proyek</Form.Label>
												<Form.Control name="nama-proyek" as="textarea" rows="3" value="PROYEK PEMBANGUNAN GEDUNG 17 LANTAI" disabled />
											</Form.Group>
										</div>
										<div className="col-md-6">
											<Form.Group controlId="exampleForm.ControlInput1">
												<Form.Label>Lokasi</Form.Label>
												<Form.Control name="lokasi-proyek" type="text" placeholder="" value="Tegal" disabled/>
											</Form.Group>
											<Form.Group controlId="exampleForm.ControlInput1">
												<Form.Label>Tahun Proyek</Form.Label>
												<Form.Control name="tahun-proyek" type="number" placeholder="" value="2018" disabled/>
											</Form.Group>
											<Form.Group controlId="exampleForm.ControlInput1">
												<Form.Label>Pemilik Proyek</Form.Label>
												<Form.Control name="pemilik-proyek" type="text" placeholder="" value="Sodara Amas" disabled/>
											</Form.Group>
										</div>
										<div className="col-md-6">
											<Form.Group controlId="exampleForm.ControlInput1">
												<Form.Label>Nilai Kontrak</Form.Label>
												<Form.Control name="nilai_kontrak_proyek" type="text" placeholder="" value="Rp. 1.xxx.xxx.xxx.xxx,-" disabled/>
											</Form.Group>
											<Form.Group controlId="exampleForm.ControlSelect1">
												<Form.Label>Jenis Kontrak</Form.Label>
												<Form.Control as="select" disabled>
													<option>Swa Kelola</option>
													<option>Kontraktual</option>
												</Form.Control>
											</Form.Group>
											<div className="row">
												<div className="col-md-6">
													<Form.Group controlId="exampleForm.ControlInput1">
														<Form.Label>Tanggal Mulai</Form.Label>
														<Form.Control name="tgl-mulai-proyek" type="text" placeholder="" value="03-03-2020" disabled/>
													</Form.Group>
												</div>
												<div className="col-md-6">
													<Form.Group controlId="exampleForm.ControlInput1">
														<Form.Label>Tanggal Selesai</Form.Label>
														<Form.Control name="tgl-selesai-proyek" type="text" placeholder="" value="05-07-2022" disabled/>
													</Form.Group>
												</div>
											</div>
										</div>
									</div>
								</div>
							</div>
						</PortletBody>
					</Portlet>
				</div>
				
				<div className="col-xl-6">
					<Portlet fluidHeight={true}>
						<PortletHeader
							title="Order Statistics"
							toolbar={
								<PortletHeaderToolbar>
									<PortletHeaderDropdown />
								</PortletHeaderToolbar>
							}
						/>
						
						<PortletBody>
							<OrderStatisticsChart />
						</PortletBody>
					</Portlet>
				</div>
			</div>
			
		</>
	);
}
