import React, {Fragment} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import {
	Button,
	Card,
	ListGroup,
	ListGroupItem,
	Nav,
	CardGroup,
	CardDeck,
	CardColumns,
	Form, InputGroup, Col, Row,
	OverlayTrigger, Tooltip,
	Table, Spinner, Badge
} from "react-bootstrap";
import HMenu from "../../../_metronic/layout/header/HMenu/HMenu"
import Topbar from "../../../_metronic/layout/header/Topbar"
import Footer from "../../../_metronic/layout/footer/Footer"
import {CardActionArea, CardActions, CardContent, CardMedia, Container, Typography} from "@material-ui/core";
import {Link} from "react-router-dom";
import axios from "../../../config/axios"
import {toast} from "react-toastify";
import TablePagination from '@material-ui/core/TablePagination';
import {connect} from "react-redux";
import RemoveRedEyeOutlinedIcon from '@material-ui/icons/RemoveRedEye';
import {
	PortletHeader,
	PortletHeaderToolbar
} from "../../partials/content/Portlet";

const classes = makeStyles(theme => ({
	root: {
		flexGrow: 1,
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',
		color: theme.palette.text.secondary,
	},
}));

const classes3 = makeStyles({
	card: {
		maxWidth: 345,
		margin: 20
	},
	media: {
		height: 140
	}
});

class HomeProject extends React.Component{
	constructor(props){
		super(props)
		this.state = {
			//start pagination
			paging          : 25,
			perPage         : 0,
			pageCount       : 0,
			currentPage     : 0,
			//end pagination
			
			//start filter
			keyword         : "",
			//end filter
			
			data            : [],
			data6           : [],
			authToken		: this.props.authToken,
			selectProject   : "",
			// id_project      : null,
			isLoading       : true
		}
		this.handleReset    = this.handleReset.bind(this)
		this.selectProject  = this.selectProject.bind(this)
	}
	
	handleReset(){
		this.setState({
			isLoading       : true,
			paging          : 25,
			keyword         : '',
		})
	}
	
	selectProject(data, item){
		window.localStorage.setItem('id_project', item.id_project)
	}
	
	loadData() {
		const config = {
			headers: {
				'Accept' 		: 'application/json',
				'Content-Type' 	: 'aplication/x-www-form-urlencoded;charset=UTF-8',
				'Authorization' : 'Bearer '+ this.state.authToken
			},
		};

		let formData = new FormData()
		formData.append('page'            , this.state.currentPage + 1)
		formData.append('paging'          , this.state.paging)
		formData.append('keyword'         , this.state.keyword)

		axios.post(`project/get`, formData, config)
			.then(res => {
					console.log('res', res)
				if(res.data.status === true){
					const result = res.data.data
					this.setState({
						data        : result.data,
						data6       : res.data.data6,
						isLoading   : false,
					})
				}else{
					toast['error'](res.data.messages)
				}
			})
			.catch(error => {
				toast['error']('Terjadi Kesalahan di Server')
				this.setState({
					isLoading   : false,
				})
			})
	}
	
	componentDidMount() {
		this.loadData()
	}
	
	render(){
		// console.log('isLoading', this.state.isLoading)
		// console.log('paging', this.state.paging)

		var no = 0;
		let {data}      = this.state
		let {data6}     = this.state
		let {isLoading} = this.state
		return (
			<Fragment>
				<div
					className={`kt-header kt-grid__item `}
					id="kt_header"
				>
					<HMenu />
					<Topbar />
				</div>
				
				<div className="kt-space-20" />
				
				<Container fixed>
					<div className={classes.root}>
						<div className="kt-portlet kt-portlet--height-fluid">
							{/*<div className="kt-portlet__head">*/}
							{/*	<div className="kt-portlet__head-label">*/}
									{/*<h2 className="kt-portlet__head-title"><strong>Project Page</strong></h2>*/}
									<PortletHeader
										title="Project Page"
										toolbar={
											<PortletHeaderToolbar>
												<Link to="/project/create-project">
													<Button
														// style={{float: "left",marginTop:20}}
														variant="success"
														size="md"
														// href="/new-project"
													>
														NEW PROJECT
													</Button>
												</Link>
											</PortletHeaderToolbar>
										}
									/>
							{/*	</div>*/}
							{/*</div>*/}
							<div className="kt-portlet__body">
								<div className="kt-widget5">
									<div className="kt-widget5__item ">
										<Grid container spacing={3}>
											{
												data6.length !== 0 ?
													data6.map((item, index) => {
														return(
															<Grid item md={4} xs={12} key={index}>
																<Card className={classes3.card} bg="primary" text="white" style={{textAlign: "center"}}>
																	<Card.Body>
																		<Card.Title>{item.project_name}</Card.Title>
																		<Card.Text>
																			Lokasi : {item.location}
																			<br/>
																			Tahun : {item.year}
																			<br/>
																			Pemilik Proyek : {item.owner}
																			<br/>
																			Status : {item.status !== true ? "OPEN" : "FINISHED"}
																		</Card.Text>
																		<Link to="/detail-project">
																			<Button
																				variant="outline-light"
																				size="md"
																				onClick={(val) => this.selectProject(val, item)}
																			>
																				Detail
																			</Button>
																		</Link>
																	</Card.Body>
																</Card>
															</Grid>
														)
													})
													:
													isLoading === true ?
													<Grid item md={12} xs={12} style={{textAlign:"center"}}>
														<Spinner animation="border" variant="primary" />
													</Grid>
														:
														<Grid item md={12} xs={12} style={{textAlign:"center"}}>
															<Card.Title>Tidak ada data ditemukan . . .</Card.Title>
														</Grid>
											}
											
											{/*<div className="kt-separator kt-separator--dashed"></div>*/}
										</Grid>
									
									</div>
								</div>
							</div>
						
						</div>
						
						<div className="kt-portlet kt-portlet--height-fluid">
							<div className="kt-portlet__head">
								<div className="kt-portlet__head-label">
									<h2 className="kt-portlet__head-title"><strong>List Project</strong></h2>
								</div>
							</div>
							<div className="kt-portlet__body">
								<div className="kt-widget5">
									<div className="kt-widget5__item ">
										<Grid container spacing={3}>
											<Grid item xs={12} md={12} style={{paddingBottom : 0}}>
												<Form.Row>
													<Form.Group controlId="show" style={{marginLeft: 5, marginRight: 5}}>
														<Form.Label>Tampilkan</Form.Label>
														<Form.Control as="select">
															<option>10</option>
															<option>25</option>
															<option>50</option>
															<option>100</option>
														</Form.Control>
													</Form.Group>
													
													<Form.Group sm={12} controlId="pencarian" style={{marginLeft: 5, marginRight: 5}}>
														<Form.Label>Pencarian</Form.Label>
														<Form.Control placeholder="Cari......" />
													</Form.Group>
													
													<Button variant="primary" style={{margin:"auto 5px auto 5px"}}>
														Submit
													</Button>
													
													<Button variant="dark"
													        onClick={this.handleReset}
													        style={{margin:"auto 5px auto 5px"}}>
														Reset
													</Button>
												
												</Form.Row>
											</Grid>
											<Grid item xs={12} md={12}>
												<Table striped bordered hover responsive>
													<thead>
													<tr>
														<th>#</th>
														<th>Nama Proyek</th>
														<th>Lokasi</th>
														<th>Tanggal Mulai</th>
														<th>Tahun</th>
														<th>Pemilik Prroyek</th>
														<th>Status</th>
														<th>Aksi</th>
													</tr>
													</thead>
													<tbody>
													{
														data.length !== 0 ?
															data.map((item, index) => {
																no +=1;
																return(
																	<tr key={index}>
																		<td>{no}</td>
																		<td>{item.project_name}</td>
																		<td>{item.location}</td>
																		<td>{item.year}</td>
																		<td>{item.year}</td>
																		<td>{item.owner}</td>
																		<td>
																			{
																				item.status !== true ?
																					<Badge variant="primary">OPEN</Badge>
																					:
																					<Badge variant="success">FINISHED</Badge>
																			}
																		</td>
																		<td>
																			<OverlayTrigger
																				key={item.id_project}
																				placement={`top`}
																				overlay={
																					<Tooltip id={`tooltip-${item.id_project}`}>
																						<strong>Lihat Detail</strong>
																					</Tooltip>
																				}
																			>
																				<Button
																					// onClick={(e) => this.handleShow(e, item)}
																				        variant="outline-primary" size="sm" style={{marginRight:5}}>
																					<RemoveRedEyeOutlinedIcon/>
																				</Button>
																			</OverlayTrigger>
																		</td>
																	</tr>
																)
															})
															:
															isLoading === true ?
																<Grid item md={12} xs={12} style={{textAlign:"center"}}>
																	<Spinner animation="border" variant="primary" />
																</Grid>
																:
																<Grid item md={12} xs={12} style={{textAlign:"center"}}>
																	<Card.Title>Tidak ada data ditemukan . . .</Card.Title>
																</Grid>
													}
													</tbody>
													<tfoot>
													<TablePagination
														rowsPerPageOptions={[5, 10, 25]}
														// colSpan={3}
														count={10}
														rowsPerPage={5}
														// page={page}
														// SelectProps={{
														// 	inputProps: { 'aria-label': 'Rows per page' },
														// 	native: true,
														// }}
														// onChangePage={handleChangePage}
														// onChangeRowsPerPage={handleChangeRowsPerPage}
														// ActionsComponent={TablePaginationActions}
													/>
													</tfoot>
												</Table>
											</Grid>
										</Grid>
									</div>
								</div>
							</div>
						</div>
					</div>
				</Container>
				
				<Footer/>
			</Fragment>
		);
	}
}

const mapStateToProps = ({ auth: { user, authToken } }) => ({
	user, authToken
});

export default connect(mapStateToProps)(HomeProject);
