import React, {Suspense, lazy, useState} from "react";
import { Redirect, Route, Switch } from "react-router-dom";
import Builder from "./Builder";

import HomeProject from "./HomeProject";
import DetailProject from "../project/DetailProject"
import CreateProject from "../project/project/CreateProject"

/*RENCANA ANGGARAN START*/
import ListRencanaAnggaran from "../project/perencanaan/ListRencanaAnggaran"
import ListApprovalRencanaAnggaran from "../project/perencanaan/ListApprovalRencanaAnggaran"
/*RENCANA ANGGARAN END*/

// PROJECT START
import ProjectOverview from "../project/project/ProjectOverview"
import ProjectBQ from "../project/project/bq"
// PROJECT END

// SITE ENG START
import ListRequestMaterial from "../project/siteEng/ListRequestMaterial"
import CreateRequestMaterial from "../project/siteEng/CreateRequestMaterial"
import ListApprovalMaterial from "../project/siteEng/ListApprovalMaterial"

import ListRequestAlat from "../project/siteEng/ListRequestAlat"
import CreateRequestAlat from "../project/siteEng/CreateRequestAlat"
import ListApprovalAlat from "../project/siteEng/ListApprovalAlat"
// SITE ENG END

// PROGRESS START
import ListProgress from "../project/progress/ListProgress"
import InputProgress from "../project/progress/InputProgress"
// PROGRESS END

// GUDANG START
import ListMaterialIn from "../project/gudang/ListMaterialIn"
import CreateMaterialIn from "../project/gudang/CreateMaterialIn"
import ListMaterialOut from "../project/gudang/ListMaterialOut"
import CreateMaterialOut from "../project/gudang/CreateMaterialOut"
import ListMaterialStock from "../project/gudang/ListMaterialStock"
// GUDANG END

// LOGISTIK START
import ListPoLogistik from "../project/logistik/ListPoLogistik"
import CreatePoLogistik from "../project/logistik/CreatePoLogistik"
// LOGISTIK END

import Dashboard from "./Dashboard";
import DocsPage from "./docs/DocsPage";
import { LayoutSplashScreen } from "../../../_metronic";

const GoogleMaterialPage = lazy(() =>
  import("./google-material/GoogleMaterialPage")
);
const ReactBootstrapPage = lazy(() =>
  import("./react-bootstrap/ReactBootstrapPage")
);

export default function HomePage(props) {
  // useEffect(() => {
  //   console.log('Home page');
  // }, []) // [] - is required if you need only one call
  // https://reactjs.org/docs/hooks-reference.html#useeffect
	return (
    <Suspense fallback={<LayoutSplashScreen />}>
      <Switch>
        {
          /* Redirect from root URL to /dashboard. */
          //<Redirect exact from="/" to="/dashboard" />
          <Redirect exact from="/" to="/home-project" />
        }
        <Route path="/builder" component={Builder} />
        
        <Route path="/home-project" component={HomeProject} />
        <Route path="/detail-project"
               component={DetailProject}
        />
        <Route path="/project/create-project" component={CreateProject} />
        
        {/*PROJECT Start*/}
	      <Route path="/project/overview" component={ProjectOverview}/>
	      <Route path="/project/bq" component={ProjectBQ}/>
        {/*PROJECT END*/}
        
	      {/*PERENCANAAN START*/}
	      <Route path="/planning/plan-budget" component={ListRencanaAnggaran}/>
	      <Route path="/planning/approval-budget" component={ListApprovalRencanaAnggaran}/>
	      {/*PERENCANAAN END*/}
	      
	      {/*SITE ENG START*/}
	      <Route path="/site-eng/req-mat" component={ListRequestMaterial} />
        <Route path="/site-eng/create-mat" component={CreateRequestMaterial} />
        <Route path="/site-eng/app-mat" component={ListApprovalMaterial} />

        <Route path="/site-eng/req-alat" component={ListRequestAlat} />
        <Route path="/site-eng/create-alat" component={CreateRequestAlat} />
        <Route path="/site-eng/app-alat" component={ListApprovalAlat} />
	      {/*SITE ENG END*/}

	      
	      {/*PROGRESS START*/}
	      <Route path="/progress/overview" component={ListProgress} />
	      <Route path="/progress/input" component={InputProgress} />
	      {/*PROGRESS END*/}
	      
	      {/*GUDANG START*/}
	      <Route path="/gudang/mat-in" component={ListMaterialIn} />
	      <Route path="/gudang/create-mat-in" component={CreateMaterialIn} />
	      <Route path="/gudang/mat-out" component={ListMaterialOut} />
	      <Route path="/gudang/create-mat-out" component={CreateMaterialOut} />
	      <Route path="/gudang/mat-stock" component={ListMaterialStock} />
	      {/*GUDANG END*/}
	      
	      {/*LOGISTIK START*/}
	      <Route path="/logistik/po" component={ListPoLogistik} />
	      <Route path="/logistik/create-po" component={CreatePoLogistik} />
	      {/*LOGISTIK END*/}
        
        <Route path="/dashboard" component={Dashboard} />
        <Route path="/google-material" component={GoogleMaterialPage} />
        <Route path="/react-bootstrap" component={ReactBootstrapPage} />
        <Route path="/docs" component={DocsPage} />
        <Redirect to="/error/error-v1" />
      </Switch>
    </Suspense>
  );
}
