import React, {Fragment} from "react"
import {Button, Form, Tab, Table} from "react-bootstrap";
import {
    Portlet,
    PortletBody,
    PortletHeader,
    PortletHeaderToolbar
} from "../../../../partials/content/Portlet";
import axios from '../../../../../config/axios';
import {toast} from "react-toastify";
import format from "format-number";
import {connect} from "react-redux";

class Struktur extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            data: [],
            file : null,
            fileBase64 : '',
            isLoading: true
        }
        this.getFile    = this.getFile.bind(this)
        this.importData = this.importData.bind(this)

    }

    getFile(){
        this.fileInput.click();
    }

    importData (){
        const fileInput = document.querySelector("#fileInput");
        const config = {
            headers: {
                'Accept' 		: 'application/json',
                'Content-Type' 	: 'aplication/x-www-form-urlencoded;charset=UTF-8;multipart/form-data',
                'Authorization' : 'Bearer '+ this.props.authToken
            },
        };

        let formData = new FormData()

        formData.append('project_id', localStorage.getItem("id_project") )
        formData.append('bq', this.state.fileBase64)

        axios.post('import-bq', formData, config)
            .then((res) => {
                let response = res.data
                if (response.status) {
                    toast['success'](response.messages)
                    this.setState({
                        file            : null,
                        isLoading       : false
                    })
                }
                else {
                    if (Array.isArray(response.messages)) {

                        response.messages.map(item => {
                            toast['error'](item)
                            return true
                        })
                    } else {
                        toast['error'](response.messages)
                    }
                }
            })
            .catch(function (error) {
                toast['error']('Terjadi Kesalahan di Server')
                console.log("error", error);
            })
    }

    loadData() {
        const config = {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'aplication/x-www-form-urlencoded;charset=UTF-8;multipart/form-data',
                'Authorization': 'Bearer ' + this.props.authToken
            },
        };

        let formData = new FormData()
        formData.append('id_project', localStorage.getItem("id_project"))
        axios.post(`project/get-project`, formData, config)
            .then(res => {
                const result = res.data.data
                if (res.status) {
                    this.setState({
                        data: result,
                        isLoading: false
                    })
                } else {
                    toast['error']('Terjadi Kesalahan Di Server')
                }
            })
            .catch(error => {
                toast['error']('Terjadi Kesalahan Di Server')
            })
    }

    componentDidMount() {
        this.loadData()
    }

    render() {
        let {data} = this.state
        return (
            <div className="row">
                <div className="col-md-12">
                    <Button variant="success" type="submit" style={{float:"left"}}>
                        Import
                    </Button>
                    <Button variant="success" type="submit" style={{float:"right"}}>
                        Download Template
                    </Button>
                </div>

                <div className="col-md-12" style={{marginTop:20}}>
                    <Table responsive striped bordered hover>
                        <thead>
                        <tr>
                            <th><strong>No</strong></th>
                            <th><strong>Uraian Pekerjaan</strong></th>
                            <th><strong>Sub Total</strong></th>
                            <th><strong>Total</strong></th>
                        </tr>
                        </thead>
                        <tbody>
                        <tr>
                            <td>1</td>
                            <td><strong>Struktur 1</strong></td>
                            <td style={{textAlign:"right"}}> <strong>Rp 44.066.359</strong></td>
                            <td style={{textAlign:"right"}}></td>
                        </tr>
                        <tr>
                            <td>2</td>
                            <td><strong>Struktur 2</strong></td>
                            <td style={{textAlign:"right"}}> <strong>Rp  138.656.133</strong></td>
                            <td style={{textAlign:"right"}}></td>
                        </tr>
                        <tr>
                            <td>3</td>
                            <td><strong>Struktur 3</strong></td>
                            <td style={{textAlign:"right"}}>  <strong>Rp 414.832.322</strong></td>
                            <td style={{textAlign:"right"}}></td>
                        </tr>
                        <tr>
                            <td><strong>Sub Total</strong></td>
                            <td><strong>Pekerjaan Struktur</strong></td>
                            <td></td>
                            <td style={{textAlign:"right"}}> <strong>Rp  597.554.813</strong></td>
                        </tr>
                        </tbody>
                    </Table>
                </div>

                <div className="col-md-12" style={{marginTop:20}}>
                    <Table responsive striped bordered hover>
                        <thead>
                        <tr>
                            <th>No</th>
                            <th>Level</th>
                            <th>Uraian Pekerjaan</th>
                            <th>Satuan</th>
                            <th>Volume</th>
                            <th>Harga Satuan</th>
                            <th>Jumlah Harga</th>
                        </tr>
                        </thead>
                        <tbody>
                        </tbody>
                    </Table>
                </div>
            </div>
        )
    }
}


const mapStateToProps = ({ auth: { user, authToken } }) => ({
    user, authToken
});

export default connect(mapStateToProps)(Struktur);