import React, {Fragment} from "react"
import Grid from "@material-ui/core/Grid";
import {Container, InputAdornment, makeStyles} from "@material-ui/core";
import {Modal, Spinner, Table} from "react-bootstrap";
import TextField from "@material-ui/core/TextField";
import {Button, Badge, Form} from  "react-bootstrap"
import {toast} from "react-toastify";
import axios from '../../../../config/axios'
import DeleteForeverOutlinedIcon from '@material-ui/icons/DeleteForeverOutlined';
import AddOutlinedIcon from '@material-ui/icons/Add';
import InlineEdit from 'react-edit-inline2';
import format from "format-number";
import {connect} from "react-redux";
import TablePagination from "@material-ui/core/TablePagination";
import {
	PortletHeader,
	PortletHeaderToolbar
} from "../../../partials/content/Portlet";
import moment from 'moment';

const useStyles = makeStyles(theme => ({
	root: {
		flexGrow: 1,
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',
		color: theme.palette.text.secondary,
	},
}));

class CreatePoLogistik extends React.Component{
	constructor(props){
		super(props)
		this.state = {
			//start pagination
			paging          : 25,
			perPage         : 0,
			pageCount       : 0,
			currentPage     : 0,
			//end pagination
			
			//req
			status_po       : false,
			dataRequest : [],
			//end req
			
			///hide Show
			show        : true,
			showBtnSave : true,
			///endhide Show
			
			price       : 0,
			supplier    : '',
			idBtn       : '',
			no_po       : '',
			no_spk      : '',
			tanggal_po  : '',
			supplier    : '',
			
			detail      : [],
			data        : [],
			isLoading   : true
		}
		this.handleChange       = this.handleChange.bind(this)
		this.handleSubmit       = this.handleSubmit.bind(this)
		this.handleAddDetail    = this.handleAddDetail.bind(this);
		this.handleRemoveDetail = this.handleRemoveDetail.bind(this);
		this.dataChanged        = this.dataChanged.bind(this);
		this.dataChangedPrice   = this.dataChangedPrice.bind(this);
		this.dataChangedSpek    = this.dataChangedSpek.bind(this);
		this.handleShow         = this.handleShow.bind(this);
		this.handleHide         = this.handleHide.bind(this);
	}
	
	handleShow(){
		// console.log('show')
		this.setState({
			show: true
		})
	}
	
	handleHide(){
		// console.log('hide')
		this.setState({
			show: false
		})
	}
	
	dataChangedSpek(data, index, item) {
		var array = [...this.state.detail]; // make a separate copy of the array
		var index = index
		if (index !== -1) {
			array.splice(index, 1);
			// this.setState({detail: array});
			this.setState(prevState => ({
				detail  : [...array, {...item,
					...prevState.item,
					specification: data.specification
				}],
				data : ''
			}))
		}
		
		
	}
	
	dataChangedPrice(data, index, item) {
		console.log('data', data)
		var array = [...this.state.detail]; // make a separate copy of the array
		var index = index
		if (index !== -1) {
			array.splice(index, 1);
			// this.setState({detail: array});
			this.setState(prevState => ({
				detail  : [...array, {...item,
					...prevState.item,
					price: data.price
				}],
			}))
		}
		this.setState({data : ""})
	}
	
	dataChanged(data, index, item) {
		var array = [...this.state.detail]; // make a separate copy of the array
		var index = index
		if (index !== -1) {
			array.splice(index, 1);
			// this.setState({detail: array});
			this.setState(prevState => ({
				detail  : [...array, {...item,
					...prevState.item,
					quantity: data.jumlah_baru
				}],
				data : ''
			}))
		}
		
		
	}
	
	handleAddDetail(e, index, item){
		console.log('i',index)
		var data = [...this.state.data];
		data.splice(item, 1);
		
		this.setState({
			detail  : [...this.state.detail, item],
			// data    :
		});
		
		var array = [...this.state.dataRequest]; // make a separate copy of the array
		// console.log('aar', array)
		var index = index
		if (index !== -1) {
			array.splice(index, 1);
			this.setState({dataRequest: array});
		}
	}
	
	handleRemoveDetail(e, index, item){
		// console.log('add det',item)
		var array = [...this.state.detail]; // make a separate copy of the array
		console.log('aar', array)
		var index = index
		if (index !== -1) {
			array.splice(index, 1);
			this.setState({detail: array});
		}
		
		var data = [...this.state.dataRequest];
		data.splice(item, 1);

		this.setState({
			dataRequest  : [...this.state.dataRequest, item],
			// data    :
		});
		
	}
	
	handleChange(event){
		
		const target = event.target;
		const value = target.type === "checkbox" ? target.checked : target.value;
		const name = target.name;
		
		this.setState({
			[name]: value
		});
	}
	
	handleSubmit(){
		//validasi front
		if(this.state.no_po == "" || this.state.no_spk == "" || this.state.tanggal_po == "" || this.state.supplier == "" || this.state.detail.length == 0){
			toast['error']('Data tidak boleh kosong')
		}else{
			const config = {
				headers: {
					'Accept' 		: 'application/json',
					'Content-Type' 	: 'aplication/x-www-form-urlencoded;charset=UTF-8;multipart/form-data',
					'Authorization' : 'Bearer '+ this.props.authToken
				},
			};
			
			let formData = new FormData()
			
			formData.append('no_purchase_order', this.state.no_po )
			formData.append('spk', this.state.no_spk )
			formData.append('purchase_order_date', this.state.tanggal_po )
			formData.append('supplier', this.state.supplier )
			formData.append('user_id', this.props.user.id)
			formData.append('project_id', localStorage.getItem("id_project"))
			formData.append('section_id', 4)
			formData.append('detail', JSON.stringify(this.state.detail) )
			
			axios.post('purchase-order/insert', formData, config)
				.then((res) => {
					let response = res.data
					console.log(res)
					if (response.status) {
						toast['success'](response.messages)
						this.setState({
							// no_po       : '',
							// no_spk      : '',
							// tanggal_po  : '',
							// supplier    : '',
							// detail      : [],
							showBtnSave     : false,
							isLoading       : false
						})
					}
					else {
						if (Array.isArray(response.messages)) {
							
							response.messages.map(item => {
								toast['error'](item)
								return true
							})
						} else {
							toast['error'](response.messages)
						}
					}
				})
				.catch(function (error) {
					toast['error']('Terjadi Kesalahan di Server')
				})
		}
		//end validasi front
	}
	
	loadData() {
		const config = {
			headers: {
				'Accept' 		: 'application/json',
				'Content-Type' 	: 'aplication/x-www-form-urlencoded;charset=UTF-8;multipart/form-data',
				'Authorization' : 'Bearer '+ this.props.authToken
			},
		};
		
		let formData = new FormData()
		formData.append('page'            , this.state.currentPage + 1)
		formData.append('paging'          , this.state.paging)
		formData.append('keyword'         , this.state.keyword)
		
		formData.append('id_project', localStorage.getItem("id_project"))
		formData.append('section_id', 4)
		formData.append('user_id', this.props.user.id)
		
		axios.post(`purchase-order/get-item-request`,formData, config)
			.then(res => {
				const result = res.data.data.data
				console.log('res', res.data)
				if(res.status){
					this.setState({
						dataRequest         : result,
						isLoading           : false
					})
				}else{
					toast['error']('Terjadi Kesalahan Di Server')
				}
			})
			.catch(error => {
				toast['error']('Terjadi Kesalahan di Server')
				this.setState({
					isLoading   : false,
				})
			})
	}
	
	componentDidMount() {
		this.loadData()
	}
	
	customValidateText(text) {
		return (text.length > 0 && text.length < 500);
	}
	
	render() {
		// console.log('st show', this.state.show)
		var noR = 0;
		var no  = 0;
		let {dataRequest} = this.state
		let {data} = this.state
		let {detail} = this.state
		let {isLoading} = this.state
		var tabelReq;
		
		if(this.state.show == true){
			tabelReq = <div className="kt-portlet__body" style={{paddingBottom: 0}}>
				<div className="kt-widget5">
					<div className="kt-widget5__item ">
						<Grid container spacing={3}>
							<Grid item md={12} xs={12}>
								<div className="kt-section">
									<div className="row">
										<div className="col-md-12">
											
											<div className="col-md-12" style={{marginTop:20}}>
												<Table responsive bordered hover>
													<thead>
													<tr>
														<th><small><strong>No</strong></small></th>
														<th><small><strong>Tanggal</strong></small></th>
														<th><small><strong>Item</strong></small></th>
														<th><small><strong>Jumlah</strong></small></th>
														<th><small><strong>Satuan</strong></small></th>
														<th><small><strong>Spesifikasi</strong></small></th>
														<th><small><strong>Aksi</strong></small></th>
													</tr>
													</thead>
													<tbody>
													{
														dataRequest.length !== 0 && dataRequest !== null ?
															(
																dataRequest.map((item, index) => {
																	noR +=1
																	return(
																		<tr key={index}>
																			<td><small>{noR}</small></td>
																			<td><small>{moment(item.request_date).format('LL')}</small></td>
																			<td><small>{item.item_name}</small></td>
																			<td><small>{item.quantity}</small></td>
																			<td><small>{item.uom}</small></td>
																			<td><small>{item.specification}</small></td>
																			<td style={{textAlign : "center"}}>
																				<Button onClick={(e) => {
																					// console.log('e', e.target.name)
																					this.handleAddDetail(e, index, item)
																				}} variant="outline-success" size="sm">
																					<AddOutlinedIcon/>
																				</Button>
																			</td>
																		</tr>
																	)
																})
															)
															:
															isLoading === true ?
																(
																	<tr>
																		<td colSpan={100} style={{textAlign:"center"}}>
																			<Spinner animation="border" variant="primary" />
																		</td>
																	</tr>
																)
																:
																(
																	<tr>
																		<td colSpan={100} style={{textAlign:"center"}}>
																			Tidak ada data ditemukan . . .
																		</td>
																	</tr>
																)
													}
													</tbody>
													<tfoot>
													{/*<TablePagination
																		rowsPerPageOptions={[this.state.perPage,5, 10, 25]}
																		colSpan={100}
																		count={this.state.pageCount}
																		rowsPerPage={this.state.perPage}
																		page={this.state.currentPage-1}
																		SelectProps={{
																			inputProps: { 'aria-label': 'Rows per page' },
																			native: true,
																		}}
																		onChangePage={this.handlePageClick}
																		// onChangeRowsPerPage={handleChangeRowsPerPage}
																		// ActionsComponent={TablePaginationActions}
																	/>*/}
													</tfoot>
												</Table>
											</div>
										
										</div>
									</div>
								</div>
							</Grid>
						</Grid>
					</div>
				</div>
			</div>
		}else{
			tabelReq = ''
		}
		return(
			<Fragment>
				{/*List Request*/}
				<Container fixed>
					<div className={useStyles.root}>
						<div className="kt-portlet kt-portlet--height-fluid">
							<PortletHeader
								title="Request Order - Site Engineer"
								toolbar={
									<PortletHeaderToolbar>
										{
											this.state.show == true ?
												<Button onClick={this.handleHide} variant="danger">Hide</Button>:
												<Button onClick={this.handleShow} variant="primary">Show</Button>
										}
									</PortletHeaderToolbar>
								}
							/>
							{tabelReq}
						</div>
					</div>
				</Container>
				<Container fixed>
					<div className={useStyles.root}>
						<div className="kt-portlet kt-portlet--height-fluid">
							<div className="kt-portlet__head">
								<div className="kt-portlet__head-label">
									<h2 className="kt-portlet__head-title"><strong>Create Purchase Order</strong></h2>
								</div>
							</div>
							<div className="kt-portlet__body">
								<div className="kt-widget5">
									<div className="kt-widget5__item ">
										<Grid container spacing={3}>
											<Grid item md={12} xs={12}>
												<div className="kt-section">
													<div className="row">
														<div className="col-md-6">
															<Form.Group controlId="exampleForm.ControlInput1">
																<Form.Label>Nomor PO</Form.Label>
																<Form.Control name="no_po" onChange={this.handleChange} type="text" placeholder="" />
															</Form.Group>
															<Form.Group controlId="exampleForm.ControlInput1">
																<Form.Label>Nomor SPK</Form.Label>
																<Form.Control name="no_spk" onChange={this.handleChange} type="text" placeholder="" />
															</Form.Group>
														</div>
														<div className="col-md-6">
															<Form.Group controlId="exampleForm.ControlInput1">
																<Form.Label>Supplier</Form.Label>
																<Form.Control name="supplier" onChange={this.handleChange} type="text" placeholder="" />
															</Form.Group>
															<Form.Group controlId="exampleForm.ControlInput1">
																<Form.Label>Tanggal</Form.Label>
																<Form.Control name="tanggal_po" onChange={this.handleChange} type="date" placeholder="" />
															</Form.Group>
														</div>
													</div>
													
													<div className="kt-space-20" />
													
													<div className="row">
														<div className="col-md-12">
															<div className="col-md-12" style={{marginTop:20}}>
																<Table responsive striped bordered hover>
																	<thead>
																	<tr>
																		<th><strong>No</strong></th>
																		<th><strong>Nama Material</strong></th>
																		<th><strong>Jumlah</strong></th>
																		<th><strong>Satuan</strong></th>
																		<th><strong>Spesifikasi</strong></th>
																		<th><strong>Harga</strong></th>
																		<th><strong>Sub Total</strong></th>
																		<th><strong>Action</strong></th>
																	</tr>
																	</thead>
																	<tbody>
																	{
																		detail.length !== 0 && detail !== null ?
																			detail.map((item, index)=>{
																				no +=1;
																				return(
																					<tr key={index}>
																						<td>{no}</td>
																						<td><strong>{item.item_name}</strong></td>
																						<td style={{textAlign: "right"}}>
																							<InlineEdit
																								validate={this.customValidateText}
																								activeClassName="editing"
																								type="number"
																								text={item.quantity}
																								paramName="jumlah_baru"
																								change={(val) => this.dataChanged(val, index, item)}
																								style={{
																									cursor:'pointer',
																								}}
																							/>
																						</td>
																						<td>{item.uom}</td>
																						<td>
																							<InlineEdit
																								validate={this.customValidateText}
																								activeClassName="editing"
																								type="number"
																								text={item.specification}
																								paramName="specification"
																								change={(val) => this.dataChangedSpek(val, index, item)}
																								style={{
																									cursor:'pointer',
																								}}
																							/>
																						</td>
																						<td style={{textAlign: "right"}}>
																							<Badge variant="primary">
																								<InlineEdit
																									validate={this.customValidateText}
																									activeClassName="editing"
																									type="number"
																									text={
																										format({prefix: 'Rp ', integerSeparator: "."})
																										(item.price == null
																											?
																											'0'
																											:
																											item.price)
																									}
																									paramName="price"
																									change={(val) => this.dataChangedPrice(val, index, item)}
																									style={{
																										cursor:'pointer',
																									}}
																								/>
																							</Badge>
																						</td>
																						<td style={{textAlign: "right"}}>
																							<Badge variant="primary">
																								{format({prefix: 'Rp ', integerSeparator: "."})(item.price * item.quantity)}
																							</Badge>
																						</td>
																						<td style={{textAlign: "center"}}>
																							<Button onClick={(e) => this.handleRemoveDetail(e, index, item)} variant="outline-danger" size="sm" style={{marginRight:5}}><DeleteForeverOutlinedIcon/></Button>
																						</td>
																					</tr>
																				)
																			})
																			:
																			<tr>
																				<td colSpan={100} style={{textAlign: "center"}}>Belum ada data ditambahkan . . .</td>
																			</tr>
																	}
																	</tbody>
																</Table>
															</div>
														</div>
													</div>
													
													{
														this.state.showBtnSave == true ?
															<div className="row">
																<div className="col-md-12">
																	<Button variant="primary" type="submit" style={{float:"right"}}
																	        onClick={this.handleSubmit}
																	>
																		Save
																	</Button>
																</div>
															</div>
															: ""
													}
													
												</div>
											</Grid>
										</Grid>
									</div>
								</div>
							</div>
						</div>
					</div>
				</Container>
			</Fragment>
		)
	}
}

const mapStateToProps = ({ auth: { user, authToken } }) => ({
	user, authToken
});

export default connect(mapStateToProps)(CreatePoLogistik);