import React, {Fragment} from "react"
import Grid from "@material-ui/core/Grid";
import {Container, InputAdornment, makeStyles} from "@material-ui/core";
import {Col, Row, Table} from "react-bootstrap";
import TextField from "@material-ui/core/TextField";
import {Button, Badge, Form} from  "react-bootstrap"
import {connect} from "react-redux";
import DeleteForeverOutlinedIcon from '@material-ui/icons/DeleteForeverOutlined';
import {toast} from "react-toastify";
import axios from '../../../../config/axios'

const useStyles = makeStyles(theme => ({
	root: {
		flexGrow: 1,
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',
		color: theme.palette.text.secondary,
	},
}));

class CreateRequestMaterial extends React.Component{
	constructor(props){
		super(props)
		
		this.state = {
			no_request          : "",
			request_date        : "",
			item_name           : "",
			uom                 : "",
			quantity            : "",
			specification       : "",
			header              : [],
			detail              : [],
			isLoading           : true,
		}
		
		this.handleChange           = this.handleChange.bind(this)
		this.handleAddMore          = this.handleAddMore.bind(this)
		this.handleRemoveDetail     = this.handleRemoveDetail.bind(this)
		this.handleSubmit           = this.handleSubmit.bind(this)
	}
	
	handleSubmit(){
		const config = {
			headers: {
				'Accept' 		: 'application/json',
				'Content-Type' 	: 'aplication/x-www-form-urlencoded;charset=UTF-8;multipart/form-data',
				'Authorization' : 'Bearer '+ this.props.authToken
			},
		};
		
		let formData = new FormData()
		formData.append('no_request', this.state.no_request)
		formData.append('request_date', this.state.request_date)
		formData.append('user_id', this.props.user.id)
		formData.append('project_id', localStorage.getItem("id_project"))
		formData.append('section_id', 4)
		formData.append('detail', JSON.stringify(this.state.detail))
		
		axios.post(`requests/insert`, formData, config)
			.then(res => {
				console.log('res', res)
				if(res.data.status === true){
					toast['success'](res.data.messages)
					this.setState({
						detail		: [],
						isLoading   : false,
					})
				}else{
					if (Array.isArray(res.data.messages)) {
						
						res.data.messages.map(item => {
							toast['error'](item)
							return true
						})
					} else {
						toast['error'](res.data.messages)
					}
				}
			})
			.catch(error => {
				toast['error']('Terjadi Kesalahan di Server')
				this.setState({
					isLoading   : false,
				})
			})
	}
	
	handleRemoveDetail(e, index){
		var array = [...this.state.detail]; // make a separate copy of the array
		console.log('aar', array)
		var index = index
		console.log('i', index)
		if (index !== -1) {
			array.splice(index, 1);
			this.setState({detail: array});
		}
	}
	
	handleChange(event){
		const target = event.target;
		const value = target.type === "checkbox" ? target.checked : target.value;
		const name = target.name;
		
		this.setState({
			[name]: value
		});
	}
	
	handleAddMore(){
		if(this.state.item_name == "" || this.state.uom == "" || this.state.quantity == ""){
			toast['error']('Data Tidak Boleh Kosong . . .')
		}else{
			var item = {
				item_name       : this.state.item_name,
				uom             : this.state.uom,
				quantity        : this.state.quantity,
				specification   : this.state.specification
			}
			
			this.setState({
				detail              : [...this.state.detail, item],
				item_name           : "",
				uom                 : "",
				quantity            : "",
				specification       : ""
			})
		}
	}
	
	render() {
		// console.log('material',this.state.item_name)
		// console.log('detail',this.state.detail)
		// console.log(this.props)
		let no = 0;
		let {detail} = this.state
		return(
			<Fragment>
				<Container fixed>
					<div className={useStyles.root}>
						<div className="kt-portlet kt-portlet--height-fluid">
							<div className="kt-portlet__head">
								<div className="kt-portlet__head-label">
									<h2 className="kt-portlet__head-title"><strong>Create Request Material</strong></h2>
								</div>
							</div>
							<div className="kt-portlet__body">
								<div className="kt-widget5">
									<div className="kt-widget5__item ">
										<Grid container spacing={3}>
										    <Grid item md={12} xs={12}>
											    <div className="kt-section">
											    	<div className="row">
											    		<div className="col-md-6">
											    			<Form.Group controlId="exampleForm.ControlInput1">
											    				<Form.Label>Kode Request</Form.Label>
											    				<Form.Control onChange={(event) => this.handleChange(event)}  name="no_request" type="text" placeholder="" />
											    			</Form.Group>
													    </div>
													    <div className="col-md-6">
											    			<Form.Group controlId="exampleForm.ControlInput2">
											    				<Form.Label>Tanggal Request</Form.Label>
											    				<Form.Control onChange={(event) => this.handleChange(event)}  name="request_date" type="date" placeholder="" />
											    			</Form.Group>
											    		</div>
											    	</div>
											    	<div className="kt-space-20" />
											    	<div className="row">
											    		<div className="col-md-12">
											    			<Form.Label><strong>Detail Material</strong></Form.Label>
											    		</div>
											    	</div>
											    	<div className="row">
													    <Col>
														    <Form.Control onChange={(event) => this.handleChange(event)} value={this.state.item_name} name="item_name" type="text" placeholder="Material"  />
													    </Col>
													    <Col>
														    <Form.Control onChange={(event) => this.handleChange(event)} value={this.state.uom} name="uom" type="text" placeholder="Satuan"  />
													    </Col>
													    <Col>
														    <Form.Control onChange={(event) => this.handleChange(event)} value={this.state.quantity} name="quantity" type="number" placeholder="Jumlah"/>
													    </Col>
													    <Col>
														    <Form.Control onChange={(event) => this.handleChange(event)} value={this.state.specification} name="specification" as="textarea" rows="2" placeholder="specification"/>
													    </Col>
													    <Col>
													        <Button
														        onClick={() => {
															        this.handleAddMore();
														        }}
														        variant="primary">
														        Tambah
													        </Button>
													    </Col>
											    	</div>
												    <div className="row">
													    <div className="col-md-12">														    
														    <div className="col-md-12" style={{marginTop:20}}>
														    	<Table responsive striped bordered hover>
														    		<thead>
														    		<tr>
														    			<th><strong>No</strong></th>
														    			<th><strong>Nama Material</strong></th>
														    			<th><strong>Satuan</strong></th>
														    			<th><strong>Jumlah</strong></th>
														    			<th><strong>specification</strong></th>
														    			<th><strong>Action</strong></th>
														    		</tr>
														    		</thead>
														    		<tbody>
																    {
																	    detail.length !== 0 && detail !== null ?
																		    detail.map((item, index)=>{
																		    	no +=1;
																			    return(
																		            <tr key={index}>
																		                <td>{no}</td>
																		                <td>{item.item_name}</td>
																		                <td>{item.uom}</td>
																		                <td>{item.quantity}</td>
																		                <td>{item.specification}</td>
																		                <td>
																			                <Button onClick={(e) => this.handleRemoveDetail(e, index)} variant="outline-danger" size="sm">
																				                <DeleteForeverOutlinedIcon/>
																			                </Button>
																	                    </td>
																		            </tr>
																			    )
																		    })
																		    :
																		    <tr>
																			    <td colSpan={6} style={{textAlign: "center"}}>Tidak ada data</td>
																		    </tr>
																    }
														    		</tbody>
														    	</Table>
														    </div>
														    <div className="col-md-12">
															    <Button
																    style={{float: "right"}}
																    onClick={() => {
																	    this.handleSubmit();
																    }}
																    variant="success">
																    SAVE
															    </Button>
														    </div>
													    </div>
												    </div>
											    </div>
										    </Grid>
										</Grid>
									</div>
								</div>
							</div>
						</div>
					</div>
				</Container>
			</Fragment>
		)
	}
}

const mapStateToProps = ({ auth: { user, authToken } }) => ({
	user, authToken
});

export default connect(mapStateToProps)(CreateRequestMaterial);