import React, {Fragment} from "react"
import Grid from "@material-ui/core/Grid";
import {Container, InputAdornment, makeStyles} from "@material-ui/core";
import {Table} from "react-bootstrap";
import TextField from "@material-ui/core/TextField";
import {Button, Badge, Form} from  "react-bootstrap"

const useStyles = makeStyles(theme => ({
	root: {
		flexGrow: 1,
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',
		color: theme.palette.text.secondary,
	},
}));

export default class CreateMaterialIn extends React.Component{
	
	render() {
		return(
			<Fragment>
				<Container fixed>
					<div className={useStyles.root}>
						<div className="kt-portlet kt-portlet--height-fluid">
							<div className="kt-portlet__head">
								<div className="kt-portlet__head-label">
									<h2 className="kt-portlet__head-title"><strong>Create Material In</strong></h2>
								</div>
							</div>
							<div className="kt-portlet__body">
								<div className="kt-widget5">
									<div className="kt-widget5__item ">
										<Grid container spacing={3}>
											<Grid item md={12} xs={12}>
												<div className="kt-section">
													<div className="row">
														<div className="col-md-6">
															<Form.Group controlId="exampleForm.ControlInput1">
																<Form.Label>No PO</Form.Label>
																<Form.Control name="no_po" type="text" placeholder="" />
															</Form.Group>
															<Form.Group controlId="exampleForm.ControlInput1">
																<Form.Label>No SPK</Form.Label>
																<Form.Control name="no_spk" type="text" placeholder="" />
															</Form.Group>
														</div>
														<div className="col-md-6">
															<Form.Group controlId="exampleForm.ControlInput1">
																<Form.Label>Tanggal Masuk</Form.Label>
																<Form.Control name="tanggal-request" type="date" placeholder="" />
															</Form.Group>
															<Form.Group controlId="exampleForm.ControlInput1">
																<Form.Label>Supplier</Form.Label>
																<Form.Control name="supplier" type="text" placeholder="" />
															</Form.Group>
														</div>
													</div>
													<div className="kt-space-20" />
													<div className="row">
														<div className="col-md-12">
															<Form.Label><strong>Detail Material</strong></Form.Label>
														</div>
													</div>
													<div className="row">
														<div className="col-md-3">
															<Form.Control name="nama-material" type="text" placeholder="Material" />
														</div>
														<div className="col-md-3">
															<Form.Control name="satuan-material" type="text" placeholder="Satuan" />
														</div>
														<div className="col-md-3">
															<Form.Control name="jumlah-material" type="text" placeholder="Jumlah" />
														</div>
														<div className="col-md-3">
															<Button variant="primary">Tambah</Button>
														</div>
													</div>
													<div className="row">
														<div className="col-md-12">
															<div className="col-md-12" style={{marginTop:20}}>
																<Table responsive striped bordered hover>
																	<thead>
																	<tr>
																		<th><strong>No</strong></th>
																		<th><strong>Nama Material</strong></th>
																		<th><strong>Satuan</strong></th>
																		<th><strong>Jumlah</strong></th>
																		<th><strong>Action</strong></th>
																	</tr>
																	</thead>
																	<tbody>
																	<tr>
																		<td>1</td>
																		<td>Bata Merah</td>
																		<td>Biji</td>
																		<td>25.000</td>
																		<td>
																			<Button variant="warning" size="sm" style={{marginRight:5}}>Edit</Button>
																			<Button variant="danger" size="sm" style={{marginRight:5}}>Hapus</Button>
																		</td>
																	</tr>
																	<tr>
																		<td>2</td>
																		<td>Semen</td>
																		<td>KG</td>
																		<td>750</td>
																		<td>
																			<Button variant="warning" size="sm" style={{marginRight:5}}>Edit</Button>
																			<Button variant="danger" size="sm" style={{marginRight:5}}>Hapus</Button>
																		</td>
																	</tr>
																	<tr>
																		<td>3</td>
																		<td>Pasir</td>
																		<td>M^3</td>
																		<td>10.000</td>
																		<td>
																			<Button variant="warning" size="sm" style={{marginRight:5}}>Edit</Button>
																			<Button variant="danger" size="sm" style={{marginRight:5}}>Hapus</Button>
																		</td>
																	</tr>
																	</tbody>
																</Table>
															</div>
														</div>
														<div className="col-md-12">
															<Button
																style={{float:"right"}}
																variant="success">
																SAVE
															</Button>
														</div>
													</div>
												</div>
											</Grid>
										</Grid>
									</div>
								</div>
							</div>
						</div>
					</div>
				</Container>
			</Fragment>
		)
	}
}