import React, {Fragment} from "react"
import Grid from "@material-ui/core/Grid";
import {Container, InputAdornment, makeStyles} from "@material-ui/core";
import {Modal, Spinner, Tab, Table, Tabs} from "react-bootstrap";
import TextField from "@material-ui/core/TextField";
import {Button, Badge} from  "react-bootstrap"
import {Link} from "react-router-dom";
import {toast} from "react-toastify";
import axios from '../../../../config/axios'
import DeleteForeverOutlinedIcon from '@material-ui/icons/DeleteForeverOutlined';
import RemoveRedEyeOutlinedIcon from '@material-ui/icons/RemoveRedEye';
import DoneOutlinedIcon from '@material-ui/icons/Done';
import ClearOutlinedIcon from '@material-ui/icons/Clear';
import moment from 'moment';
import { connect } from "react-redux";

const useStyles = makeStyles(theme => ({
	root: {
		flexGrow: 1,
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',
		color: theme.palette.text.secondary,
	},
}));

class ListApprovalMaterial extends React.Component{
	constructor(props){
		super(props)

		this.state = {
			//start pagination
			paging          : 25,
			perPage         : 0,
			pageCount       : 0,
			currentPage     : 0,
			//end pagination
			
			no_req      : "",
			showDetail  : false,
			show        : false,
			data        : [],
			dataManager : [],
			children    : [],
			isLoading   : true,
		}

		this.handleShow                 = this.handleShow.bind(this);
		this.handleClose                = this.handleClose.bind(this);
		this.switchApproveSiteManager   = this.switchApproveSiteManager.bind(this)
		this.rejectApproveSiteManager   = this.rejectApproveSiteManager.bind(this)
		this.switchApproveManager       = this.switchApproveManager.bind(this)
		this.rejectApproveManager       = this.rejectApproveManager.bind(this)
	}
	
	switchApproveManager(data, item){
		const config = {
			headers: {
				'Accept' 		: 'application/json',
				'Content-Type' 	: 'aplication/x-www-form-urlencoded;charset=UTF-8;multipart/form-data',
				'Authorization' : 'Bearer '+ this.props.authToken
			},
		};
		
		let formData = new FormData()
		
		formData.append('id_request'            , item.id_request)
		formData.append('user_id_manager'       , this.props.user.id)
		formData.append('status_manager'        , 1)
		formData.append('note_manager'          , 'Approved by Manager')
		formData.append('status_site_manager'   , 1)
		formData.append('note_site_manager'     , 'Approved by Site Manager')
		
		axios.post('requests/approval-manager', formData, config)
			.then((res) => {
				let response = res.data
				console.log(response)
				if (response.status) {
					toast['success'](response.messages)
				}
				else {
					if (Array.isArray(response.messages)) {
						
						response.messages.map(item => {
							toast['error'](item)
							return true
						})
					} else {
						toast['error'](response.messages)
					}
				}
				this.loadDataSiteManager()
				this.loadDataManager()
				this.setState({
					isLoading           : false
				})
			})
			.catch(error => {
				toast['error']('Terjadi Kesalahan di Server')
				this.setState({
					isLoading   : false,
				})
			})
	}
	
	rejectApproveManager(data, item){
		const config = {
			headers: {
				'Accept' 		: 'application/json',
				'Content-Type' 	: 'aplication/x-www-form-urlencoded;charset=UTF-8;multipart/form-data',
				'Authorization' : 'Bearer '+ this.props.authToken
			},
		};
		
		let formData = new FormData()
		
		formData.append('id_request'            , item.id_request)
		formData.append('user_id_manager'       , this.props.user.id)
		formData.append('status_manager'        , 2)
		formData.append('note_manager'          , 'REJECTED by Manager')
		formData.append('status_site_manager'   , 2)
		formData.append('note_site_manager'     , 'REJECTED by Site Manager')
		
		axios.post('requests/approval-manager', formData, config)
			.then((res) => {
				let response = res.data
				console.log(response)
				if (response.status) {
					toast['success'](response.messages)
				}
				else {
					if (Array.isArray(response.messages)) {
						
						response.messages.map(item => {
							toast['error'](item)
							return true
						})
					} else {
						toast['error'](response.messages)
					}
				}
				this.loadDataSiteManager()
				this.loadDataManager()
				this.setState({
					isLoading           : false
				})
			})
			.catch(error => {
				toast['error']('Terjadi Kesalahan di Server')
				this.setState({
					isLoading   : false,
				})
			})
	}
	
	switchApproveSiteManager(data, item){
		const config = {
			headers: {
				'Accept' 		: 'application/json',
				'Content-Type' 	: 'aplication/x-www-form-urlencoded;charset=UTF-8;multipart/form-data',
				'Authorization' : 'Bearer '+ this.props.authToken
			},
		};
		
		let formData = new FormData()
		
		formData.append('id_request'            , item.id_request)
		formData.append('user_id_site_manager'  , this.props.user.id)
		formData.append('status_site_manager'   , 1)
		formData.append('note_site_manager'     , 'Approved by Site Manager')
		
		axios.post('requests/approval-site-manager', formData, config)
			.then((res) => {
				let response = res.data
				console.log(response)
				if (response.status) {
					toast['success'](response.messages)
				}
				else {
					if (Array.isArray(response.messages)) {
						
						response.messages.map(item => {
							toast['error'](item)
							return true
						})
					} else {
						toast['error'](response.messages)
					}
				}
				this.loadDataSiteManager()
				this.loadDataManager()
				this.setState({
					isLoading           : false
				})
			})
			.catch(error => {
				toast['error']('Terjadi Kesalahan di Server')
				this.setState({
					isLoading   : false,
				})
			})
	}
	
	rejectApproveSiteManager(data, item){
		const config = {
			headers: {
				'Accept' 		: 'application/json',
				'Content-Type' 	: 'aplication/x-www-form-urlencoded;charset=UTF-8;multipart/form-data',
				'Authorization' : 'Bearer '+ this.props.authToken
			},
		};
		
		let formData = new FormData()
		
		formData.append('id_request'            , item.id_request)
		formData.append('user_id_site_manager'  , this.props.user.id)
		formData.append('status_site_manager'   , 2)
		formData.append('note_site_manager'     , 'REJECTED by Site Manager')
		
		axios.post('requests/approval-site-manager', formData, config)
			.then((res) => {
				let response = res.data
				console.log(response)
				if (response.status) {
					toast['success'](response.messages)
				}
				else {
					if (Array.isArray(response.messages)) {
						
						response.messages.map(item => {
							toast['error'](item)
							return true
						})
					} else {
						toast['error'](response.messages)
					}
				}
				this.loadDataSiteManager()
				this.loadDataManager()
				this.setState({
					isLoading           : false
				})
			})
			.catch(error => {
				toast['error']('Terjadi Kesalahan di Server')
				this.setState({
					isLoading   : false,
				})
			})
	}
	
	handleClose() {
		this.setState({
			no_req  : "",
			show: false,
			modalShow: false,
			children:null
		});
	}
	
	handleShow(e, item) {
		// console.log('asf', item)
		let child = []
		item.children.map((ch, idx)=>{
			child.push(ch)
		})
		this.setState({
			no_req  : item.no_request,
			modalShow: true,
			show: true,
			children:child
		});
	}
	
	loadDataSiteManager() {
		const config = {
			headers: {
				'Accept' 		: 'application/json',
				'Content-Type' 	: 'aplication/x-www-form-urlencoded;charset=UTF-8;multipart/form-data',
				'Authorization' : 'Bearer '+ this.props.authToken
			},
		};
		
		let formData = new FormData()
		formData.append('page'            , this.state.currentPage + 1)
		formData.append('paging'          , this.state.paging)
		formData.append('keyword'         , this.state.keyword)
		
		formData.append('project_id', localStorage.getItem("id_project"))
		formData.append('section_id', 4)
		formData.append('user_id', this.props.user.id)
		axios.post(`requests/get`,formData, config)
			.then(res => {
				// const result = res.data.data
				var result;
				if(res.data.data !== undefined){
					result = res.data.data.data
				}else{
					result = []
				}
				console.log('res', result)
				if(res.status){
					this.setState({
						data        : result,
						isLoading   : false
					})
				}else{
					toast['error']('Terjadi Kesalahan Di Server')
				}
			})
			.catch(error => {
				toast['error']('Terjadi Kesalahan di Server')
				this.setState({
					isLoading   : false,
				})
			})
	}
	
	loadDataManager() {
		const config = {
			headers: {
				'Accept' 		: 'application/json',
				'Content-Type' 	: 'aplication/x-www-form-urlencoded;charset=UTF-8;multipart/form-data',
				'Authorization' : 'Bearer '+ this.props.authToken
			},
		};
		
		let formData = new FormData()
		
		formData.append('page'            , this.state.currentPage + 1)
		formData.append('paging'          , this.state.paging)
		formData.append('keyword'         , this.state.keyword)
		
		formData.append('project_id', localStorage.getItem("id_project"))
		formData.append('section_id', 4)
		formData.append('user_id', this.props.user.id)
		axios.post(`requests/get-manager`,formData, config)
			.then(res => {
				console.log('res manager', res)
				var result;
				if(res.data.data !== undefined){
					result = res.data.data.data
				}else{
					result = []
				}
				console.log('res', result)
				if(res.data.status){
					this.setState({
						dataManager : result,
						isLoading   : false
					})
				}else{
					toast['error']('Terjadi Kesalahan Di Server')
				}
			})
			.catch(error => {
				toast['error']('Terjadi Kesalahan di Server')
				this.setState({
					isLoading   : false,
				})
			})
	}
	
	componentDidMount() {
		this.loadDataSiteManager()
		this.loadDataManager()
	}
	
	render() {
		// console.log('props', this.props.user)
		let {children} = this.state
		let {data} = this.state
		let {dataManager} = this.state
		let {isLoading} = this.state
		var tabelDetail;
		if (this.state.modalShow === true) {
			tabelDetail = (
				<Modal
					show={this.state.modalShow}
					onHide={this.handleClose}
					size="lg"
					aria-labelledby="contained-modal-title-vcenter"
					centered
				>
					<Modal.Header closeButton>
						<Modal.Title id="contained-modal-title-vcenter">
							{this.state.no_req}
						</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<Table responsive striped bordered hover>
							<thead>
							<tr>
								<th><strong>No</strong></th>
								<th><strong>Item</strong></th>
								<th><strong>Satuan</strong></th>
								<th><strong>Jumlah</strong></th>
							</tr>
							</thead>
							<tbody>
							{
								children.length !== 0 && children !== null ?
									children.map((itemx, idx) => {
										return (
											<tr key={idx}>
												<td>1</td>
												<td>{itemx.item_name}</td>
												<td>{itemx.uom}</td>
												<td>{itemx.quantity}</td>
											</tr>
										)
									})
									:
									""
							}
							</tbody>
						</Table>
					</Modal.Body>
					<Modal.Footer>
						<Button onClick={this.handleClose}>Close</Button>
					</Modal.Footer>
				</Modal>
			)
		}else{
			tabelDetail = ""
		}
		return(
			<Fragment>
				<Container fixed>
					{tabelDetail}
					<div className={useStyles.root}>
						<div className="kt-portlet kt-portlet--height-fluid">
							<div className="kt-portlet__head">
								<div className="kt-portlet__head-label">
									<h2 className="kt-portlet__head-title"><strong>Approval Material</strong></h2>
								</div>
							</div>
							<div className="kt-portlet__body">
								<div className="kt-widget5">
									<div className="kt-widget5__item ">
										<Grid container spacing={3}>
										    <Grid item md={12} xs={12}>
											    <div className="kt-section">
												    <div className="row">
													    <div className="col-md-12">
														
														    <Tabs defaultActiveKey={this.props.user.level === 'Manager' ? "Manager" : "Site-Manager"}
															          id="tab-manager">
															    <Tab eventKey="Site-Manager" title="Site-Manager">
																    <div className="col-md-12" style={{marginTop:20}}>
																        <Table responsive striped bordered hover>
																            <thead>
																            <tr>
																                <th><strong>No</strong></th>
																                <th><strong>Tanggal</strong></th>
																                <th><strong>Nomor Request</strong></th>
																                <th><strong>Status</strong></th>
																                <th><strong>Note</strong></th>
																                <th><strong>Action</strong></th>
																            </tr>
																            </thead>
																            <tbody>
																		    {
																			    data.length !== 0 && data !== null ?
																				    (
																					    data.map((item, index) => {
																						    return(
																							    <tr key={index}>
																								    <td>1</td>
																								    <td>{moment(item.request_date).format('LL')}</td>
																								    <td>{item.no_request}</td>
																								    <td>
																									    {
																										    item.status_site_manager == 0 ?
																											    <Badge variant="primary">NEW</Badge> :
																											    item.status_site_manager == 1 ?
																												    <Badge variant="success">APPROVED</Badge> :
																												    <Badge variant="danger">REJECTED</Badge>
																									    }
																								    </td>
																								    <td>{item.note_site_manager}</td>
																								    <td style={{textAlign:"center"}}>
																									    {
																										    item.status_site_manager == 0 ?
																											    <Fragment>
																												    <Button onClick={(e) => this.handleShow(e, item)} variant="outline-primary" size="sm" style={{marginRight:5}}><RemoveRedEyeOutlinedIcon/></Button>
																												    <Button onClick={(val) => this.switchApproveSiteManager(val, item)} variant="outline-success" size="sm" style={{marginRight:5}}><DoneOutlinedIcon/></Button>
																												    <Button onClick={(val) => this.rejectApproveSiteManager(val, item)} variant="outline-danger" size="sm" style={{marginRight:5}}><ClearOutlinedIcon/></Button>
																											    </Fragment>
																											    :
																											    item.status_site_manager == 1 ?
																												    <Fragment>
																													    <Button onClick={(e) => this.handleShow(e, item)} variant="outline-primary" size="sm" style={{marginRight:5}}><RemoveRedEyeOutlinedIcon/></Button>
																													    <Button onClick={(val) => this.rejectApproveSiteManager(val, item)} variant="outline-danger" size="sm" style={{marginRight:5}}><ClearOutlinedIcon/></Button>
																												    </Fragment>
																												    :
																												    <Fragment>
																													    <Button onClick={(e) => this.handleShow(e, item)} variant="outline-primary" size="sm" style={{marginRight:5}}><RemoveRedEyeOutlinedIcon/></Button>
																													    <Button onClick={(val) => this.switchApproveSiteManager(val, item)} variant="outline-success" size="sm" style={{marginRight:5}}><DoneOutlinedIcon/></Button>
																												    </Fragment>
																									    }
																								    </td>
																							    </tr>
																						    )
																					    })
																				    )
																				    :
																				    isLoading === true ?
																					    (
																						    <tr>
																							    <td colSpan={6} style={{textAlign:"center"}}>
																								    <Spinner animation="border" variant="primary" />
																							    </td>
																						    </tr>
																					    )
																					    :
																					    (
																						    <tr>
																							    <td colSpan={6} style={{textAlign:"center"}}>
																								    Tidak ada data ditemukan . . .
																							    </td>
																						    </tr>
																					    )
																		    }
																            </tbody>
																        </Table>
																    </div>
															    </Tab>
															    {
															    	this.props.user.level !== 'Manager' ?
																    ""
																    :
																	    <Tab eventKey="Manager" title="Manager">
																		    <div className="col-md-12" style={{marginTop:20}}>
																			    <Table responsive striped bordered hover>
																				    <thead>
																				    <tr>
																					    <th><strong>No</strong></th>
																					    <th><strong>Tanggal</strong></th>
																					    <th><strong>Nomor Request</strong></th>
																					    <th><strong>Status</strong></th>
																					    <th><strong>Note</strong></th>
																					    <th><strong>Action</strong></th>
																				    </tr>
																				    </thead>
																				    <tbody>
																				    {
																					    dataManager.length !== 0 && dataManager !== null ?
																						    (
																							    dataManager.map((item, index) => {
																								    return(
																									    <tr key={index}>
																										    <td>1</td>
																										    <td>{moment(item.request_date).format('LL')}</td>
																										    <td>{item.no_request}</td>
																										    <td>
																											    {
																												    item.status_manager == 0 ?
																													    <Badge variant="primary">NEW</Badge> :
																													    item.status_manager == 1 ?
																														    <Badge variant="success">APPROVED</Badge> :
																														    <Badge variant="danger">REJECTED</Badge>
																											    }
																										    </td>
																										    <td>{item.note_manager}</td>
																										    <td style={{textAlign:"center"}}>
																											    {
																												    item.status_manager == 0 ?
																													    <Fragment>
																														    <Button onClick={(e) => this.handleShow(e, item)} variant="outline-primary" size="sm" style={{marginRight:5}}><RemoveRedEyeOutlinedIcon/></Button>
																														    <Button onClick={(val) => this.switchApproveManager(val, item)} variant="outline-success" size="sm" style={{marginRight:5}}><DoneOutlinedIcon/></Button>
																														    <Button onClick={(val) => this.rejectApproveManager(val, item)} variant="outline-danger" size="sm" style={{marginRight:5}}><ClearOutlinedIcon/></Button>
																													    </Fragment>
																													    :
																													    item.status_manager == 1 ?
																														    <Fragment>
																															    <Button onClick={(e) => this.handleShow(e, item)} variant="outline-primary" size="sm" style={{marginRight:5}}><RemoveRedEyeOutlinedIcon/></Button>
																															    <Button onClick={(val) => this.rejectApproveManager(val, item)} variant="outline-danger" size="sm" style={{marginRight:5}}><ClearOutlinedIcon/></Button>
																														    </Fragment>
																														    :
																														    <Fragment>
																															    <Button onClick={(e) => this.handleShow(e, item)} variant="outline-primary" size="sm" style={{marginRight:5}}><RemoveRedEyeOutlinedIcon/></Button>
																															    <Button onClick={(val) => this.switchApproveManager(val, item)} variant="outline-success" size="sm" style={{marginRight:5}}><DoneOutlinedIcon/></Button>
																														    </Fragment>
																											    }
																										    </td>
																									    </tr>
																								    )
																							    })
																						    )
																						    :
																						    isLoading === true ?
																							    (
																								    <tr>
																									    <td colSpan={6} style={{textAlign:"center"}}>
																										    <Spinner animation="border" variant="primary" />
																									    </td>
																								    </tr>
																							    )
																							    :
																							    (
																								    <tr>
																									    <td colSpan={6} style={{textAlign:"center"}}>
																										    Tidak ada data ditemukan . . .
																									    </td>
																								    </tr>
																							    )
																				    }
																				    </tbody>
																			    </Table>
																		    </div>
																	    </Tab>
															    }
															    
														    </Tabs>

													    </div>
												    </div>
											    </div>
										    </Grid>
										</Grid>
									</div>
								</div>
							</div>
						</div>
					</div>
				</Container>
			</Fragment>
		)
	}
}

const mapStateToProps = ({ auth: { user, authToken } }) => ({
	user, authToken
});

export default connect(mapStateToProps)(ListApprovalMaterial);