import React, {Fragment} from "react"
import {Container, makeStyles} from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import {Badge, Button, Table} from "react-bootstrap";
import {Link} from "react-router-dom";


const useStyles = makeStyles(theme => ({
	root: {
		flexGrow: 1,
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',
		color: theme.palette.text.secondary,
	},
}));

export default class ListMaterialOut extends React.Component{
	constructor(props){
		super(props)
		
		this.state = {
			showDetail : false
		}
		
		this.handleShow = this.handleShow.bind(this)
	}
	
	handleShow = (e) => {
		this.setState((prevState)=>({
			showDetail:!prevState.showDetail
		}))
		
	}
	
	render(){
		var tabelDetail;
		if (this.state.showDetail === true) {
			tabelDetail = (
				<tr>
					<td colSpan={6}>
						<div className="col-md-12" style={{marginTop:20}}>
							<Table responsive striped bordered hover>
								<thead>
								<tr>
									<th><strong>No</strong></th>
									<th><strong>Nama Material</strong></th>
									<th><strong>Satuan</strong></th>
									<th><strong>Jumlah</strong></th>
								</tr>
								</thead>
								<tbody>
								<tr>
									<td>1</td>
									<td>Bata Merah</td>
									<td>Biji</td>
									<td>25.000</td>
								</tr>
								<tr>
									<td>2</td>
									<td>Semen</td>
									<td>KG</td>
									<td>750</td>
								</tr>
								<tr>
									<td>3</td>
									<td>Pasir</td>
									<td>M^3</td>
									<td>10.000</td>
								</tr>
								</tbody>
							</Table>
						</div>
					</td>
				</tr>
			)
		}else{
			tabelDetail = ""
		}
		return(
			<Fragment>
				<Container fixed>
					<div className={useStyles.root}>
						<div className="kt-portlet kt-portlet--height-fluid">
							<div className="kt-portlet__head">
								<div className="kt-portlet__head-label">
									<h2 className="kt-portlet__head-title"><strong>Material Out</strong></h2>
								</div>
							</div>
							<div className="kt-portlet__body">
								<div className="kt-widget5">
									<div className="kt-widget5__item ">
										<Grid container spacing={3}>
											<Grid item md={12} xs={12}>
												<div className="kt-section">
													<div className="row">
														<Link to="/gudang/create-mat-out">
															<Button variant="success">Tambah</Button>
														</Link>
														
														<div className="col-md-12" style={{marginTop:20}}>
															<Table responsive striped bordered hover>
																<thead>
																<tr>
																	<th><strong>No</strong></th>
																	<th><strong>Tanggal</strong></th>
																	<th><strong>No SPK</strong></th>
																	<th><strong>Tujuan</strong></th>
																	<th><strong>Status</strong></th>
																	<th><strong>Action</strong></th>
																</tr>
																</thead>
																<tbody>
																<tr>
																	<td>1</td>
																	<td>04-03-2020</td>
																	<td>
																		SPK-ProjekA-001
																	</td>
																	<td>Pelaksana</td>
																	<td><Badge variant="primary">New</Badge></td>
																	<td>
																		<Button variant="warning" size="sm" style={{marginRight:5}}><i className="fa fa-pencil"></i></Button>
																		<Button variant="danger" size="sm" style={{marginRight:5}}><i className="fa fa-trash"></i></Button>
																		<Button
																			onClick={(e) => this.handleShow(e)}
																			variant="primary" size="sm" style={{marginRight:5}}><i className="fa fa-eye"></i></Button>
																	</td>
																</tr>
																
																{tabelDetail}
																
																<tr>
																	<td>2</td>
																	<td>02-03-2020</td>
																	<td>
																		SPK-ProjekA-001
																	</td>
																	<td>Pelaksana</td>
																	<td><Badge variant="primary">New</Badge></td>
																	<td>
																		<Button variant="warning" size="sm" style={{marginRight:5}}><i className="fa fa-pencil"></i></Button>
																		<Button variant="danger" size="sm" style={{marginRight:5}}><i className="fa fa-trash"></i></Button>
																		<Button
																			onClick={(e) => this.handleShow(e)}
																			variant="primary" size="sm" style={{marginRight:5}}><i className="fa fa-eye"></i></Button>
																	</td>
																</tr>
																
																</tbody>
															</Table>
														</div>
													
													</div>
												</div>
											</Grid>
										</Grid>
									</div>
								</div>
							</div>
						</div>
					</div>
				</Container>
			</Fragment>
		)
	}
}