import React, {Fragment} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import {
	Button,
	Card,
	ListGroup,
	ListGroupItem,
	Nav,
	CardGroup,
	CardDeck,
	CardColumns,
	Table
} from "react-bootstrap";
import HMenu from "../../../_metronic/layout/header/HMenu/HMenu"
import Topbar from "../../../_metronic/layout/header/Topbar"
import Footer from "../../../_metronic/layout/footer/Footer"
import {CardActionArea, CardActions, CardContent, CardMedia, Container, Typography} from "@material-ui/core";
import {Link, Redirect} from "react-router-dom";

import "../../../_metronic/_assets/css/tileview.css"

const classes = makeStyles(theme => ({
	root: {
		flexGrow: 1,
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',
		color: theme.palette.text.secondary,
	},
}));

const classes3 = makeStyles({
	card: {
		maxWidth: 345,
		minHeight: 180,
		margin: 20,
		/*width: "90%",*/
		// display: "flex",
		justifyContent: "center"
	},
	media: {
		height: 140
	}
});
// const classes = useStyles();
// const classes3 = useStyles3();


export default class DetailProject extends React.Component{
	constructor(props){
		super(props)
		this.state = {
			pageName    : "",
		}
		this.setPageName = this.setPageName.bind(this)
	}
	
	setPageName(data){
		console.log('d', data)
		this.setState({
			pageName : data,
		})
			localStorage.setItem('pageName', data)
	}
	//
	// const InitialPage = window.localStorage.getItem("pageName") || ""
	// const [pageName, setPageName] = useState("");
	//
	// useEffect(() => {
	// 	window.localStorage.setItem('pageName', pageName)
	// })
	
	render() {
		let {pageName} = this.state
			console.log('page name', pageName)
		return (
			<Fragment>
				{
					pageName !== "" ?
						(pageName == "project" ? <Redirect to="/project/overview"/>
							: pageName == "perencanaan" ? <Redirect to="/planning/plan-budget"/>
								: pageName == "progress" ? <Redirect to="/progress/overview"/>
									: pageName == "siteEng" ? <Redirect to="/site-eng/req-mat"/>
										: pageName == "gudang" ? <Redirect to="/gudang/mat-in"/>
											: pageName == "logistik" ? <Redirect to="/logistik/po"/>
												: pageName == "keuangan" ? <Redirect to="/dashboard"/>
													: pageName == "report" ? <Redirect to="/dashboard"/>
														: <Redirect to="/dashboard"/>)
						:
						null
				}
				<div
					className={`kt-header kt-grid__item `}
					id="kt_header"
				>
					<HMenu/>
					<Topbar/>
				</div>
				
				<div className="kt-space-20"/>
				
				<Container fixed>
					<div className={classes.root}>
						<div className="kt-portlet kt-portlet--height-fluid">
							<div className="kt-portlet__head">
								<div className="kt-portlet__head-label">
									<h2 className="kt-portlet__head-title"><strong>Project Name</strong></h2>
								</div>
							</div>
							<div className="kt-portlet__body">
								<div className="kt-widget5">
									<div className="kt-widget5__item ">
										<Grid container spacing={3}>
											{/*project*/}
											<Grid item md={3} xs={12}>
												<div className="wrapper" onClick={(data) => this.setPageName("project")}>
													<div className="cardx">
														<h2>
															<span className="enclosed"><strong>PRO</strong></span>JECT
														</h2>
													</div>
												</div>
											</Grid>
											{/*perencanaan*/}
											<Grid item md={3} xs={12}>
												<div className="wrapper" onClick={() => this.setPageName("perencanaan")}>
													<div className="cardx">
														<h2>
															<span className="enclosed"><strong>PE</strong></span>RENCANAAN
														</h2>
													</div>
												</div>
											</Grid>
											
											<Grid item md={3} xs={12}>
												{/*siteEng*/}
												<div className="wrapper" onClick={() => this.setPageName("siteEng")}>
													<div className="cardx">
														<h2>
															<span className="enclosed"><strong>SITE</strong></span>ENGINEERING
														</h2>
													</div>
												</div>
											</Grid>
											{/*logistik*/}
											<Grid item md={3} xs={12}>
												<div className="wrapper" onClick={() => this.setPageName("logistik")}>
													<div className="cardx">
														<h2>
															<span className="enclosed"><strong>LO</strong></span>GISTIK
														</h2>
													</div>
												</div>
											</Grid>
											{/*gudang*/}
											<Grid item md={3} xs={12}>
												<div className="wrapper" onClick={() => this.setPageName("gudang")}>
													<div className="cardx">
														<h2>
															<span className="enclosed"><strong>GU</strong></span>DANG
														</h2>
													</div>
												</div>
											</Grid>
											{/*progress*/}
											<Grid item md={3} xs={12}>
												<div className="wrapper" onClick={() => this.setPageName("progress")}>
													<div className="cardx">
														<h2>
															<span className="enclosed"><strong>PRO</strong></span>GRESS
														</h2>
													</div>
												</div>
											</Grid>
											<Grid item md={3} xs={12}>
												{/*keuangan*/}
												<div className="wrapper" onClick={() => this.setPageName("keuangan")}>
													<div className="cardx">
														<h2>
															<span className="enclosed"><strong>KE</strong></span>UANGAN
														</h2>
													</div>
												</div>
											</Grid>
											<Grid item md={3} xs={12}>
												{/*report*/}
												<div className="wrapper" onClick={() => this.setPageName("report")}>
													<div className="cardx">
														<h2>
															<span className="enclosed"><strong>RE</strong></span>PORT
														</h2>
													</div>
												</div>
											</Grid>
											
											{/*<Grid item md={3} xs={12}>
											<div className="wrapper">
												<div className="cardx">
													<h2>
														<span className="enclosed"><strong>Cut</strong></span><strong>out</strong>
													</h2>
												</div>
											</div>
										</Grid>*/}
											
											{/*<Grid item md={3} xs={12}>
											<Card className={classes3.card} bg="primary" text="white" style={{textAlign: "center"}}>
												<Button
													variant="outline-light"
													size="md"
													onClick={() => setPageName("report")}
													style={{
														margin: 5,
														height: "13rem",
														justifyContent: "center"
													}}
												>
													<h3><strong>REPORT</strong></h3>
												</Button>
											</Card>
										</Grid>*/}
										
										</Grid>
									
									</div>
								</div>
							</div>
						
						</div>
					</div>
				</Container>
				
				<Footer/>
			</Fragment>
		);
	}
}
