import React, {Fragment} from "react"
import Grid from "@material-ui/core/Grid";
import {Container, InputAdornment, makeStyles} from "@material-ui/core";
import {Modal, Spinner, Table} from "react-bootstrap";
import TextField from "@material-ui/core/TextField";
import {Button, Badge} from  "react-bootstrap"
import {Link} from "react-router-dom";
import {toast} from "react-toastify";
import axios from '../../../../config/axios'
import RemoveRedEyeOutlinedIcon from '@material-ui/icons/RemoveRedEye';
import DoneOutlinedIcon from '@material-ui/icons/Done';
import ClearOutlinedIcon from '@material-ui/icons/Clear';

const useStyles = makeStyles(theme => ({
	root: {
		flexGrow: 1,
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',
		color: theme.palette.text.secondary,
	},
}));

export default class ListApprovalAlat extends React.Component{
	constructor(props){
		super(props)
		this.state = {
			no_req      : "",
			show        : false,
			modalShow   : false,
			children    : [],
			data        : [],
			isLoading   : true,
		}
		this.handleShow = this.handleShow.bind(this)
		this.handleClose = this.handleClose.bind(this);
		this.switchApprove = this.switchApprove.bind(this)
		this.rejectApprove = this.rejectApprove.bind(this)
	}
	
	switchApprove(data, item){
		const config = {
			headers: { "content-type": "multipart/form-data" }
		};
		
		let formData = new FormData()
		
		formData.append('id_request'            , item.id_request)
		formData.append('status'                , 1)
		formData.append('note'                  , '-')
		
		axios.post('requests/update-approval', formData, config)
			.then((res) => {
				let response = res.data
				console.log(response)
				if (response.status) {
					toast['success'](response.messages)
				}
				else {
					if (Array.isArray(response.messages)) {
						
						response.messages.map(item => {
							toast['error'](item)
							return true
						})
					} else {
						toast['error'](response.messages)
					}
				}
				this.loadData()
				this.setState({
					isLoading           : false
				})
			})
			.catch(error => {
				toast['error']('Terjadi Kesalahan di Server')
				this.setState({
					isLoading   : false,
				})
			})
	}
	
	rejectApprove(data, item){
		const config = {
			headers: { "content-type": "multipart/form-data" }
		};
		
		let formData = new FormData()
		
		formData.append('id_request'            , item.id_request)
		formData.append('status'                , 2)
		formData.append('note'                  , 'REJECTED')
		
		axios.post('requests/update-approval', formData, config)
			.then((res) => {
				let response = res.data
				console.log(response)
				if (response.status) {
					toast['success'](response.messages)
				}
				else {
					if (Array.isArray(response.messages)) {
						
						response.messages.map(item => {
							toast['error'](item)
							return true
						})
					} else {
						toast['error'](response.messages)
					}
				}
				this.loadData()
				this.setState({
					isLoading           : false
				})
			})
			.catch(error => {
				toast['error']('Terjadi Kesalahan di Server')
				this.setState({
					isLoading   : false,
				})
			})
	}
	
	handleClose() {
		this.setState({
			no_req  : "",
			show: false,
			modalShow: false,
			children:null
		});
	}
	
	handleShow(e, item) {
		// console.log('asf', item)
		let child = []
		item.children.map((ch, idx)=>{
			child.push(ch)
		})
		this.setState({
			no_req  : item.no_request,
			modalShow: true,
			show: true,
			children:child
		});
	}
	
	loadData() {
		const config = {
			headers: { "content-type": "multipart/form-data" }
		};
		
		let formData = new FormData()
		formData.append('project_id', localStorage.getItem("id_project"))
		formData.append('section_id', 5)
		formData.append('user_id', 1)
		axios.post(`requests/get`,formData, config)
			.then(res => {
				const result = res.data.data
				console.log('res', res)
				if(res.status){
					this.setState({
						data        : result,
						isLoading   : false
					})
				}else{
					toast['error']('Terjadi Kesalahan Di Server')
				}
			})
			.catch(error => {
				toast['error']('Terjadi Kesalahan di Server')
				this.setState({
					isLoading   : false,
				})
			})
	}
	
	componentDidMount() {
		this.loadData()
	}
	
	render() {
		let {children} = this.state
		let {data} = this.state
		let {isLoading} = this.state
		var tabelDetail;
		if (this.state.modalShow === true) {
			tabelDetail = (
				<Modal
					show={this.state.modalShow}
					onHide={this.handleClose}
					size="lg"
					aria-labelledby="contained-modal-title-vcenter"
					centered
				>
					<Modal.Header closeButton>
						<Modal.Title id="contained-modal-title-vcenter">
							{this.state.no_req}
						</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<Table responsive striped bordered hover>
							<thead>
							<tr>
								<th style={{textAlign:"center"}}><strong>No</strong></th>
								<th style={{textAlign:"center"}}><strong>Item</strong></th>
								<th style={{textAlign:"center"}}><strong>Satuan</strong></th>
								<th style={{textAlign:"center"}}><strong>Spesifikasi</strong></th>
								<th style={{textAlign:"center"}}><strong>Jumlah</strong></th>
							</tr>
							</thead>
							<tbody>
							{
								children.length !== 0 && children !== null ?
									children.map((itemx, idx) => {
										return (
											<tr key={idx}>
												<td>1</td>
												<td>{itemx.item_name}</td>
												<td style={{textAlign:"center"}}>{itemx.uom}</td>
												<td style={{textAlign:"center"}}>{itemx.specification}</td>
												<td style={{textAlign:"center"}}>{itemx.quantity}</td>
											</tr>
										)
									})
									:
									""
							}
							</tbody>
						</Table>
					</Modal.Body>
					<Modal.Footer>
						<Button onClick={this.handleClose}>Close</Button>
					</Modal.Footer>
				</Modal>
			)
		}else{
			tabelDetail = ""
		}
		return(
			<Fragment>
				<Container fixed>
					{tabelDetail}
					<div className={useStyles.root}>
						<div className="kt-portlet kt-portlet--height-fluid">
							<div className="kt-portlet__head">
								<div className="kt-portlet__head-label">
									<h2 className="kt-portlet__head-title"><strong>Approval Alat</strong></h2>
								</div>
							</div>
							<div className="kt-portlet__body">
								<div className="kt-widget5">
									<div className="kt-widget5__item ">
										<Grid container spacing={3}>
											<Grid item md={12} xs={12}>
												<div className="kt-section">
													<div className="row">
														<div className="col-md-12">
															
															<div className="col-md-12" style={{marginTop:20}}>
																<Table responsive striped bordered hover>
																	<thead>
																	<tr>
																		<th style={{textAlign:"center"}}><strong>No</strong></th>
																		<th style={{textAlign:"center"}}><strong>Nomor Request</strong></th>
																		<th style={{textAlign:"center"}}><strong>Status</strong></th>
																		<th style={{textAlign:"center"}}><strong>Note</strong></th>
																		<th style={{textAlign:"center"}}><strong>Action</strong></th>
																	</tr>
																	</thead>
																	<tbody>
																	{
																		data.length !== 0 && data !== null ?
																			(
																				data.map((item, index) => {
																					return(
																						<tr key={index}>
																							<td>1</td>
																							<td>{item.no_request}</td>
																							<td style={{textAlign:"center"}}>
																								{
																									item.status == 0 ?
																										<Badge variant="primary">NEW</Badge> :
																										item.status == 1 ?
																											<Badge variant="success">APPROVED</Badge> :
																											<Badge variant="danger">REJECTED</Badge>
																								}
																							</td>
																							<td style={{textAlign:"center"}}>{item.note}</td>
																							<td style={{textAlign:"center"}}>
																								{
																									item.status == 0 ?
																										<Fragment>
																											<Button onClick={(e) => this.handleShow(e, item)} variant="outline-primary" size="sm" style={{marginRight:5}}><RemoveRedEyeOutlinedIcon/></Button>
																											<Button onClick={(val) => this.switchApprove(val, item)} variant="outline-success" size="sm" style={{marginRight:5}}><DoneOutlinedIcon/></Button>
																											<Button onClick={(val) => this.rejectApprove(val, item)} variant="outline-danger" size="sm" style={{marginRight:5}}><ClearOutlinedIcon/></Button>
																										</Fragment>
																										:
																										item.status == 1 ?
																											<Fragment>
																												<Button onClick={(e) => this.handleShow(e, item)} variant="outline-primary" size="sm" style={{marginRight:5}}><RemoveRedEyeOutlinedIcon/></Button>
																												<Button onClick={(val) => this.rejectApprove(val, item)} variant="outline-danger" size="sm" style={{marginRight:5}}><ClearOutlinedIcon/></Button>
																											</Fragment>
																											:
																											<Fragment>
																												<Button onClick={(e) => this.handleShow(e, item)} variant="outline-primary" size="sm" style={{marginRight:5}}><RemoveRedEyeOutlinedIcon/></Button>
																												<Button onClick={(val) => this.switchApprove(val, item)} variant="outline-success" size="sm" style={{marginRight:5}}><DoneOutlinedIcon/></Button>
																											</Fragment>
																								}
																							</td>
																						</tr>
																					)
																				})
																			)
																			:
																			isLoading === true ?
																				(
																					<tr>
																						<td colSpan={5} style={{textAlign:"center"}}>
																							<Spinner animation="border" variant="primary" />
																						</td>
																					</tr>
																				)
																				:
																				(
																					<tr>
																						<td colSpan={5} style={{textAlign:"center"}}>
																							Tidak ada data ditemukan . . .
																						</td>
																					</tr>
																				)
																	}
																	</tbody>
																</Table>
															</div>
														
														</div>
													</div>
												</div>
											</Grid>
										</Grid>
									</div>
								</div>
							</div>
						</div>
					</div>
				</Container>
			</Fragment>
		)
	}
}