import React, {Fragment} from "react"
import {Button, Form, Tab, Table, Tabs} from "react-bootstrap";
import {
    Portlet,
    PortletBody,
    PortletHeader,
    PortletHeaderToolbar
} from "../../../../partials/content/Portlet";
import axios from '../../../../../config/axios';
import {toast} from "react-toastify";
import format from "format-number";
import {connect} from "react-redux";
import Grid from "@material-ui/core/Grid";
import Arsitektur from "./Arsitektur";
import Mech from "./Mech";
import Struktur from "./Struktur";
import Landscape from "./Landscape";

class BillofQuantity extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            data        : [],
            isLoading   : true
        }
    }

    loadData() {
        const config = {
            headers: {
                'Accept' 		: 'application/json',
                'Content-Type' 	: 'aplication/x-www-form-urlencoded;charset=UTF-8;multipart/form-data',
                'Authorization' : 'Bearer '+ this.props.authToken
            },
        };

        let formData = new FormData()
        formData.append('id_project', localStorage.getItem("id_project"))
        axios.post(`project/get-project`,formData, config)
            .then(res => {
                const result = res.data.data
                if(res.status){
                    this.setState({
                        data        : result,
                        isLoading   : false
                    })
                }else{
                    toast['error']('Terjadi Kesalahan Di Server')
                }
            })
            .catch(error => {
                toast['error']('Terjadi Kesalahan Di Server')
            })
    }

    componentDidMount() {
        this.loadData()
    }

    render(){
        let {data} = this.state
        return(
            <div className="row">
                <div className="col-xl-6">
                    <Portlet fluidHeight={true}>
                        <PortletHeader
                            title="BILL OF QUANTITY"
                        />

                        <PortletBody>
                            <div className="kt-widget12">
                                <div className="kt-widget12__content">
                                    <div className="row">
                                        <Grid container spacing={3}>
                                            <Grid item md={12} xs={12}>
                                                <div className="kt-section">
                                                    <Tabs defaultActiveKey="Arsitektur" id="uncontrolled-tab-example">
                                                        <Tab eventKey="Arsitektur" title="Arsitektur">
                                                             <Arsitektur />
                                                        </Tab>
                                                        <Tab eventKey="Mech" title="Mech Electical">
                                                            <Mech />
                                                        </Tab>
                                                        <Tab eventKey="Struktur" title="Struktur">
                                                            <Struktur />
                                                        </Tab>
                                                        <Tab eventKey="Landscape" title="Landscape">
                                                            <Landscape />
                                                        </Tab>
                                                    </Tabs>
                                                </div>
                                            </Grid>
                                        </Grid>
                                    </div>
                                </div>
                            </div>
                        </PortletBody>
                    </Portlet>
                </div>
            </div>
        )
    }
}

const mapStateToProps = ({ auth: { user, authToken } }) => ({
    user, authToken
});

export default connect(mapStateToProps)(BillofQuantity);