import React, {Fragment} from "react"
import Grid from "@material-ui/core/Grid";
import {Container, InputAdornment, makeStyles} from "@material-ui/core";
import {Modal, Spinner, Table} from "react-bootstrap";
import TextField from "@material-ui/core/TextField";
import {Button, Badge} from  "react-bootstrap"
import {Link} from "react-router-dom";
import {toast} from "react-toastify";
import axios from '../../../../config/axios'
import DeleteForeverOutlinedIcon from '@material-ui/icons/DeleteForeverOutlined';
import RemoveRedEyeOutlinedIcon from '@material-ui/icons/RemoveRedEye';
import EditOutlinedIcon from '@material-ui/icons/Edit';
import {connect} from "react-redux";
import moment from 'moment';
import {
	PortletHeader,
	PortletHeaderToolbar
} from "../../../partials/content/Portlet";
import TablePagination from "@material-ui/core/TablePagination";
import Paginates from "../../../components/Paginates"
import format from "format-number";
const useStyles = makeStyles(theme => ({
	root: {
		flexGrow: 1,
	},
	paper: {
		padding: theme.spacing(2),
		textAlign: 'center',
		color: theme.palette.text.secondary,
	},
}));

class ListPoLogistik extends React.Component{
	constructor(props){
		super(props)
		this.state = {
			//start pagination
			paging          : 25,
			perPage         : 0,
			pageCount       : 0,
			currentPage     : 0,
			//end pagination
			
			///hide Show
			showReq        : true,
			///endhide Show
			
			//data Request
			dataRequest : [],
			
			//data PO
			no_po      : "",
			
			show        : false,
			modalShow   : false,
			children    : [],
			dataPO      : [],
			isLoading   : true,
		}
		this.handleShow     = this.handleShow.bind(this);
		this.handleClose    = this.handleClose.bind(this);
		this.handleShowReq     = this.handleShowReq.bind(this);
		this.handleHideReq     = this.handleHideReq.bind(this);
	}
	
	handleShowReq(){
		// console.log('show')
		this.setState({
			showReq: true
		})
	}
	
	handleHideReq(){
		// console.log('hide')
		this.setState({
			showReq: false
		})
	}
	
	handleClose() {
		this.setState({
			no_po  : "",
			show: false,
			modalShow: false,
			children:null
		});
	}
	
	handleShow(e, item) {
		// console.log('asf', item)
		let child = []
		item.children.map((ch, idx)=>{
			child.push(ch)
		})
		this.setState({
			no_po  : item.no_purchase_order,
			modalShow: true,
			show: true,
			children:child
		});
	}
	
	loadDataPO() {
		const config = {
			headers: {
				'Accept' 		: 'application/json',
				'Content-Type' 	: 'aplication/x-www-form-urlencoded;charset=UTF-8;multipart/form-data',
				'Authorization' : 'Bearer '+ this.props.authToken
			},
		};
		
		let formData = new FormData()
		
		formData.append('page'            , this.state.currentPage + 1)
		formData.append('paging'          , this.state.paging)
		formData.append('keyword'         , this.state.keyword)
		
		formData.append('project_id', localStorage.getItem("id_project"))
		formData.append('id_user', this.props.user.id)
		axios.post(`purchase-order/get`,formData, config)
			.then(res => {
				const result = res.data.data.data
				console.log('res', res.data.status)
				if(res.data.status){
					this.setState({
						dataPO      : result,
						isLoading   : false
					})
				}else{
					// toast['error']('Terjadi Kesalahan Di Server')
					this.setState({
						isLoading   : false,
					})
				}
			})
			.catch(error => {
				toast['error']('Terjadi Kesalahan di Server')
				this.setState({
					isLoading   : false,
				})
			})
	}
	
	loadDataRequest() {
		const config = {
			headers: {
				'Accept' 		: 'application/json',
				'Content-Type' 	: 'aplication/x-www-form-urlencoded;charset=UTF-8;multipart/form-data',
				'Authorization' : 'Bearer '+ this.props.authToken
			},
		};
		
		let formData = new FormData()
		
		formData.append('page'            , this.state.currentPage + 1)
		formData.append('paging'          , this.state.paging)
		formData.append('keyword'         , this.state.keyword)
		
		formData.append('id_project', localStorage.getItem("id_project"))
		formData.append('id_user', this.props.user.id)
		axios.post(`purchase-order/get-item-request`,formData, config)
			.then(res => {
				const result = res.data.data.data
				// console.log('res req', res)
				if(res.data.status){
					this.setState({
						//pagination
						currentPage : res.data.data.current_page,
						perPage     : res.data.data.per_page,
						pageCount   : res.data.data.total,
						//end pagination
						dataRequest : result,
						isLoading   : false
					})
				}else{
					// toast['error']('Terjadi Kesalahan Di Server')
					this.setState({
						isLoading   : false,
						showReq     : false
					})
				}
			})
			.catch(error => {
				toast['error']('Terjadi Kesalahan di Server')
				this.setState({
					isLoading   : false,
				})
			})
	}
	
	componentDidMount() {
		this.loadDataRequest()
		this.loadDataPO()
	}
	
	render() {
		// console.log('pageCount',this.state.pageCount)
		let {children} = this.state
		let {dataPO} = this.state
		let {dataRequest} = this.state
		let {isLoading} = this.state
		var noR = 0, noP = 0;
		var tabelDetail;
		var tabelReq;
		if (this.state.modalShow === true) {
			tabelDetail = (
				<Modal
					show={this.state.modalShow}
					onHide={this.handleClose}
					size="lg"
					aria-labelledby="contained-modal-title-vcenter"
					centered
				>
					<Modal.Header closeButton>
						<Modal.Title id="contained-modal-title-vcenter">
							{this.state.no_po}
						</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<Table responsive striped bordered hover>
							<thead>
							<tr>
								<th><strong>No</strong></th>
								<th><strong>Item</strong></th>
								<th><strong>Satuan</strong></th>
								<th><strong>Jumlah</strong></th>
							</tr>
							</thead>
							<tbody>
							{
								children.length !== 0 && children !== null ?
									children.map((itemx, idx) => {
										return (
											<tr key={idx}>
												<td>1</td>
												<td>{itemx.item_name}</td>
												<td>{itemx.uom}</td>
												<td>{itemx.quantity}</td>
											</tr>
										)
									})
									:
									""
							}
							</tbody>
						</Table>
					</Modal.Body>
					<Modal.Footer>
						<Button onClick={this.handleClose}>Close</Button>
					</Modal.Footer>
				</Modal>
			)
		}else{
			tabelDetail = ""
		}
		
		if(this.state.showReq == true){
			tabelReq = <div className="kt-portlet__body" style={{paddingBottom: 0}}>
				<div className="kt-widget5">
					<div className="kt-widget5__item ">
						<Grid container spacing={3}>
							<Grid item md={12} xs={12}>
								<div className="kt-section">
									<div className="row">
										<div className="col-md-12">
											
											<div className="col-md-12" style={{marginTop:20}}>
												<Table responsive bordered hover>
													<thead>
													<tr>
														<th><small><strong>No</strong></small></th>
														<th><small><strong>Tanggal</strong></small></th>
														<th><small><strong>Item</strong></small></th>
														<th><small><strong>Jumlah</strong></small></th>
														<th><small><strong>Satuan</strong></small></th>
														<th><small><strong>Spesifikasi</strong></small></th>
													</tr>
													</thead>
													<tbody>
													{
														dataRequest.length !== 0 && dataRequest !== null ?
															(
																dataRequest.map((item, index) => {
																	noR +=1
																	return(
																		<tr key={index}>
																			<td><small>{noR}</small></td>
																			<td><small>{moment(item.request_date).format('LL')}</small></td>
																			<td><small>{item.item_name}</small></td>
																			<td><small>{item.quantity}</small></td>
																			<td><small>{item.uom}</small></td>
																			<td><small>{item.specification}</small></td>
																		</tr>
																	)
																})
															)
															:
															isLoading === true ?
																(
																	<tr>
																		<td colSpan={100} style={{textAlign:"center"}}>
																			<Spinner animation="border" variant="primary" />
																		</td>
																	</tr>
																)
																:
																(
																	<tr>
																		<td colSpan={100} style={{textAlign:"center"}}>
																			Tidak ada data ditemukan . . .
																		</td>
																	</tr>
																)
													}
													</tbody>
													<tfoot>
													{/*<TablePagination
																		rowsPerPageOptions={[this.state.perPage,5, 10, 25]}
																		colSpan={100}
																		count={this.state.pageCount}
																		rowsPerPage={this.state.perPage}
																		page={this.state.currentPage-1}
																		SelectProps={{
																			inputProps: { 'aria-label': 'Rows per page' },
																			native: true,
																		}}
																		onChangePage={this.handlePageClick}
																		// onChangeRowsPerPage={handleChangeRowsPerPage}
																		// ActionsComponent={TablePaginationActions}
																	/>*/}
													</tfoot>
												</Table>
											</div>
										
										</div>
									</div>
								</div>
							</Grid>
						</Grid>
					</div>
				</div>
			</div>
		}else{
			tabelReq = ''
		}
		return(
			<Fragment>
				
				{/*List Request*/}
				<Container fixed>
					<div className={useStyles.root}>
						<div className="kt-portlet kt-portlet--height-fluid">
							<PortletHeader
								title="Request Order - Site Engineer"
								toolbar={
									<PortletHeaderToolbar>
										{
											this.state.showReq == true ?
												<Button onClick={this.handleHideReq} variant="danger">Hide</Button>:
												<Button onClick={this.handleShowReq} variant="primary">Show</Button>
										}
									</PortletHeaderToolbar>
								}
							/>
							{tabelReq}
						</div>
					</div>
				</Container>
				
				{/*List PO*/}
				<Container fixed>
					{tabelDetail}
					<div className={useStyles.root}>
						<div className="kt-portlet kt-portlet--height-fluid">
							<PortletHeader
								title="Purchase Order"
								toolbar={
									<PortletHeaderToolbar>
										<Link to="/logistik/create-po">
											<Button variant="success">Tambah</Button>
										</Link>
									</PortletHeaderToolbar>
								}
							/>
							<div className="kt-portlet__body">
								<div className="kt-widget5">
									<div className="kt-widget5__item ">
										<Grid container spacing={3}>
											<Grid item md={12} xs={12}>
												<div className="kt-section">
													<div className="row">
														<div className="col-md-12">
															<div className="col-md-12" style={{marginTop:20}}>
																<Table responsive striped bordered hover>
																	<thead>
																	<tr>
																		<th><strong>No</strong></th>
																		<th><strong>Tanggal</strong></th>
																		<th><strong>PO / SPK</strong></th>
																		<th><strong>Supplier</strong></th>
																		<th><strong>Status</strong></th>
																		<th><strong>Note</strong></th>
																		<th><strong>Total Harga</strong></th>
																		<th><strong>Action</strong></th>
																	</tr>
																	</thead>
																	<tbody>
																	{
																		dataPO.length !== 0 && dataPO !== null ?
																			(
																				dataPO.map((item, index) => {
																					noP += 1;
																					return(
																						<tr key={index}>
																							<td>{noP}</td>
																							<td>{moment(item.purchase_order_date).format('LL')}</td>
																							<td>
																								<small> PO : {item.no_purchase_order}</small>
																								<br/>
																								<small> SPK : {item.spk}</small>
																								
																							</td>
																							<td>{item.supplier}</td>
																							<td>
																								Site Manager : {" "}
																								{
																									item.status_site_manager == 0 ?
																										<Badge variant="primary">NEW</Badge> :
																										item.status_site_manager == 1 ?
																											<Badge variant="success">APPROVED</Badge> :
																											<Badge variant="danger">REJECTED</Badge>
																								}
																								<br/>
																								Manager : {" "}
																								{
																									item.status_manager == 0 ?
																										<Badge variant="primary">NEW</Badge> :
																										item.status_manager == 1 ?
																											<Badge variant="success">APPROVED</Badge> :
																											<Badge variant="danger">REJECTED</Badge>
																								}
																							</td>
																							<td>
																								Site Manager : {item.note_site_manager}
																								<br/>
																								Manager : {item.note_manager}
																							</td>
																							<td style={{textAlign: "right"}}>
																								{
																									format({prefix: 'Rp ', integerSeparator: "."})
																									(item.total == null
																										?
																										'0'
																										:
																										item.total)
																								}
																							</td>
																							<td style={{textAlign:"center"}}>
																								{
																									item.status == 0 ?
																										<Fragment>
																											<Button onClick={(e) => this.handleShow(e, item)} variant="outline-primary" size="sm" style={{marginRight:5}}><RemoveRedEyeOutlinedIcon/></Button>
																											<Button variant="outline-warning" size="sm" style={{marginRight:5}}><EditOutlinedIcon/></Button>
																											<Button variant="outline-danger" size="sm" style={{marginRight:5}}><DeleteForeverOutlinedIcon/></Button>
																										</Fragment>
																										:
																										item.status == 1 ?
																											<Button onClick={(e) => this.handleShow(e, item)} variant="outline-primary" size="sm"
																											        style={{marginRight:5,
																												        padding: ".25rem .4rem",
																												        fontSize: ".875rem",
																												        lineHeight: .5,
																												        borderRadius: ".2rem",
																											        }}>
																												<RemoveRedEyeOutlinedIcon/></Button>
																											:
																											<Fragment>
																												<Button onClick={(e) => this.handleShow(e, item)} variant="outline-primary" size="sm"
																												        style={{marginRight:5,
																													        padding: ".25rem .4rem",
																													        fontSize: ".875rem",
																													        lineHeight: .5,
																													        borderRadius: ".2rem",
																												        }}>
																													<RemoveRedEyeOutlinedIcon/></Button>
																												<Button variant="outline-warning" size="sm"
																												        style={{marginRight:5,
																															padding: ".25rem .4rem",
																															fontSize: ".875rem",
																															lineHeight: .5,
																															borderRadius: ".2rem",
																														}}>
																													<EditOutlinedIcon/></Button>
																												<Button variant="outline-danger" size="sm"
																												        style={{marginRight:5,
																															padding: ".25rem .4rem",
																															fontSize: ".875rem",
																															lineHeight: .5,
																															borderRadius: ".2rem",
																														}}>
																													<DeleteForeverOutlinedIcon/></Button>
																											</Fragment>
																								}
																							</td>
																						</tr>
																					)
																				})
																			)
																			:
																			isLoading === true ?
																				(
																					<tr>
																						<td colSpan={100} style={{textAlign:"center"}}>
																							<Spinner animation="border" variant="primary" />
																						</td>
																					</tr>
																				)
																				:
																				(
																					<tr>
																						<td colSpan={100} style={{textAlign:"center"}}>
																							Tidak ada data ditemukan . . .
																						</td>
																					</tr>
																				)
																	}
																	{/*<tr>
														    			<td>1</td>
														    			<td>REQ-001-A</td>
														    			<td><Badge variant="primary">New</Badge></td>
														    			<td>-</td>
														    			<td style={{textAlign:"center"}}>
														    				<Button variant="outline-primary" size="sm" style={{marginRight:5}}><RemoveRedEyeOutlinedIcon/></Button>
														    				<Button variant="outline-warning" size="sm" style={{marginRight:5}}><EditOutlinedIcon/></Button>
														    				<Button variant="outline-danger" size="sm" style={{marginRight:5}}><DeleteForeverOutlinedIcon/></Button>
													    				</td>
														    		</tr>*/}
																	</tbody>
																</Table>
															</div>
														
														</div>
													</div>
												</div>
											</Grid>
										</Grid>
									</div>
								</div>
							</div>
						</div>
					</div>
				</Container>
			</Fragment>
		)
	}
}

const mapStateToProps = ({ auth: { user, authToken } }) => ({
	user, authToken
});

export default connect(mapStateToProps)(ListPoLogistik);